<template>
  <div>
    <!-- МАССОВЫЕ ДЕЙСТВИЯ - НАЗНАЧИТЬ ТЕГИ -->
    <div style="float: left;">
      <v-tooltip bottom>
        <template v-slot:activator="{ on }">
          <v-btn
            small
            :disabled="bundles.selected.length === 0"
            color="primary"
            text
            style="min-width: 0; width: 30px; margin-top: 1px;"
            @click="$store.dispatch('bundles/openDialog', 'multipleAssignTags')"
            v-on="on"
          >
            <v-icon :size="12">
              fas fa-tags
            </v-icon>
          </v-btn>
        </template>
        <span>{{ $t('common.tags') }}</span>
      </v-tooltip>
    </div>
 
    <!-- МАССОВЫЕ ДЕЙСТВИЯ - ПОДЕЛИТЬСЯ -->
    <div style="float: left;">
      <v-tooltip bottom>
        <template v-slot:activator="{ on }">
          <v-btn
            small
            :disabled="bundles.selected.length === 0"
            color="primary"
            text
            style="min-width: 0; width: 30px; margin-top: 1px;"
            @click="$store.dispatch('bundles/openDialog', 'multipleShare')"
            v-on="on"
          >
            <v-icon :size="12">
              fas fa-share-alt
            </v-icon>
          </v-btn>
        </template>
        <span>{{ $t('common.share') }}</span>
      </v-tooltip>
    </div>

    <!-- МАССОВЫЕ ДЕЙСТВИЯ - ДУБЛИРОВАТЬ -->
    <div style="float: left;">
      <v-tooltip bottom>
        <template v-slot:activator="{ on }">
          <v-btn
            small
            :disabled="bundles.selected.length === 0"
            color="primary"
            text
            style="min-width: 0; width: 30px; margin-top: 1px;"
            @click="multipleDuplicate"
            v-on="on"
          >
            <v-icon :size="12">
              fas fa-copy
            </v-icon>
          </v-btn>
        </template>
        <span>{{ $t('common.duplicate') }}</span>
      </v-tooltip>
    </div>

    <!-- МАССОВЫЕ ДЕЙСТВИЯ - АРХИВИРОВАТЬ -->
    <div style="float: left;">
      <v-tooltip bottom>
        <template v-slot:activator="{ on }">
          <v-btn
            small
            :disabled="bundles.selected.length === 0"
            color="primary"
            text
            style="min-width: 0; width: 30px; margin-top: 1px;"
            @click="multipleArchive"
            v-on="on"
          >
            <v-icon :size="12">
              fas fa-archive
            </v-icon>
          </v-btn>
        </template>
        <span>
          {{ $t('common.archive') }}
        </span>
      </v-tooltip>
    </div>

    <!-- МАССОВЫЕ ДЕЙСТВИЯ - УДАЛИТЬ -->
    <div style="float: left;">
      <v-tooltip bottom>
        <template v-slot:activator="{ on }">
          <v-btn
            small
            :disabled="bundles.selected.length === 0"
            color="primary"
            text
            style="min-width: 0; width: 30px; margin-top: 1px;"
            @click="multipleDelete"
            v-on="on"
          >
            <v-icon :size="12">
              fas fa-trash
            </v-icon>
          </v-btn>
        </template>
        <span>
          {{ $t('common.delete') }}
        </span>
      </v-tooltip>
    </div>

    <!-- ПРОЧИЕ ДЕЙСТВИЯ -->
    <div style="float: left;">
      <v-menu
        offset-y
        open-on-hover
        close-on-click
      >
        <template v-slot:activator="{ on }">
          <v-btn
            :disabled="bundles.selected.length === 0"
            small
            color="primary"
            text
            style="min-width: 0; width: 30px; margin-top: 1px;"
            v-on="on"
          >
            <v-icon :size="12">
              fas fa-ellipsis-v
            </v-icon>
          </v-btn>
        </template>
        <v-list>
          <!-- РАЗАРХИВИРОВАТЬ -->
          <v-list-item
            dense
            @click="unarchive"
          >
            <v-list-item-title>{{ $t('common.unarchive') }}</v-list-item-title>
          </v-list-item>
        </v-list>
      </v-menu>
    </div>

    <!-- МАССОВЫЕ ДЕЙСТВИЯ - ИНДИКАТОР -->
    <div style="float: left;">
      <div
        class="body-2 ml-3"
        :style="{color: bundles.selected.length > 0 ? '' : 'gray', paddingTop: '5px'}"
      >
        {{ $t('common.selected') }}: {{ bundles.selected.length }}
      </div>
    </div>
  </div>
</template>

<script>
import {mapGetters} from 'vuex';

export default {
  name: 'UploadBundlesTopbarActions',

  computed: {
    ...mapGetters({
      bundles: 'bundles/bundles'
    })
  },
  methods: {
    multipleDuplicate() {
      if (!confirm(this.$t('common.confirmPlease'))) return;
      this.$store.dispatch(
        'bundles/multipleDuplicateBundles',
        this.bundles.selected.map(bundle => bundle.id)
      );
      this.$store.dispatch('bundles/clearSelected');
    },

    multipleDelete() {
      if (!confirm(this.$t('common.confirmPlease'))) return;
      this.$store.dispatch(
        'bundles/multipleDeleteBundles',
        this.bundles.selected.map(bundle => bundle.id)
      );
      this.$store.dispatch('bundles/clearSelected');
    },

    multipleArchive() {
      if (!confirm(this.$t('common.confirmPlease'))) return;
      this.$store.dispatch(
        'bundles/multipleArchiveBundles',
        this.bundles.selected.map(bundle => bundle.id)
      );
      this.$store.dispatch('bundles/clearSelected');
    },

    unarchive() {
      this.$store.dispatch('bundles/multipleUnarchiveAccounts', this.bundles.selected.map(bundle => bundle.id));
      this.$store.dispatch('bundles/clearSelected');
    }
  }
};
</script>