<template>
  <div class="d-flex align-center pr-1">
    <!-- СТАТУС -->
    <v-progress-linear
      :value="statusProgressPercent" 
      :color="statusProgressColor"
      :height="10"
      rounded
    />
    <div class="ml-2">
      <!-- СТАТУС С ОШИБКОЙ -->
      <span 
        v-if="parseInt(item.has_error, 10) === 1"
      >
        <v-icon color="red">warning</v-icon>
      </span>

      <!-- СТАТУС 6 -->
      <span 
        v-if="parseInt(item.status, 10) === 6"
      >
        <v-icon color="success">check_circle</v-icon>
      </span>

      <!-- СТАТУС В ПРОЦЕССЕ -->
      <span
        v-if="item.status < 6 && parseInt(item.has_error, 10) === 0"
        style="color: #2196F3;"
      >
        {{ statusProgressPercent + '%' }}
      </span>
    </div>
  </div>
</template>

<script>
export default {
  name: 'UploadJobsMainTableStatus',

  props: {
    item: {
      type: Object,
      default: () => ({})
    }
  },
  
  data() {
    return {
      updateInterval: null,
    };
  },

  computed: {
    statusProgressPercent() {
      if (parseInt(this.item.has_error, 10) === 1) { 
        return 100;
      }
      return (this.item.status / 6 * 100).toFixed();
    },

    statusProgressColor() {
      if (parseInt(this.item.has_error, 10) === 1) { 
        return 'red';
      }
      if (parseInt(this.item.status, 10) === 6) {
        return 'success';
      }
      return 'primary';
    }
  },

