import UploadBundlesMainTablePlacements             from './UploadBundlesMainTablePlacements';
import UploadBundlesMainTableUploadButton           from './UploadBundlesMainTableUploadButton';

export default {
  name: 'UploadBundlesMainTable',

  components: {
    UploadBundlesMainTableDemography,
    UploadBundlesMainTableDevices,
    UploadBundlesMainTableGeo,
    UploadBundlesMainTableInfo,
    UploadBundlesMainTablePlacements,
    UploadBundlesMainTableUploadButton
  },

  data() {
    return {
      cols: [
        {
          text: 'ID',
          value: 'id',
          width: 35,
        },
        {
          // text: this.$t('common.wizard'),
          value: 'wizard',
          width: 48,
        },
        {
          text: this.$t('common.bundle'),
          value: 'bundle',
          width: 230,
        },
        {
          text: this.$t('common.geo'),
          value: 'geo',
          width: 200,
        },
        {
          text: this.$t('common.placements'),
          value: 'placements',
        },
        {
          text: this.$t('common.demography'),
          value: 'demography',
          width: 200,
        },
        {
          text: this.$t('common.devices'),
          value: 'devices',
          width: 200,
        }
      ]
    };
  },

  computed: {
    ...mapGetters({
      bundles: 'bundles/bundles',
      innerHeight: 'main/innerHeight',
      profile: 'main/profile'
    })
  },

  methods: {
    selectItem(item) {
      this.$store.dispatch('bundles/setSelected', item);
    }
  }

};
</script>

