<template>
  <v-navigation-drawer
    :value="true"
    temporary
    right
    absolute
    style="z-index: 9999; width: 400px;"
    @transitionend="$emit('close')"
  >
    <div style="padding: 16px">
      <div class="mb-3">
        <span>
          <v-btn
            color="grey"
            fab
            x-small
            outlined
            class="sidebar-collapse-button"
            @click="$emit('close')"
          >
            <v-icon>keyboard_arrow_right</v-icon>
          </v-btn>
        </span>
        <span class="title">
          {{ $t('common.filters') }}
        </span>
      </div>

      <v-row>
        <!-- ФИЛЬТР ПО ТЕГАМ -->
        <v-col
          cols="12"
          class="py-0 mt-2"
        >
          <v-combobox
            :label="$t('common.tags')"
            :items="currentLevelTags.all"
            :value="filters.tags"
            multiple
            chips
            deletable-chips
            clearable
            dense
            small-chips
            solo
            prepend-inner-icon="fas fa-tags"
            hide-details
            @change="filterTags"
          />
        </v-col>

        <!-- ФИЛЬТР ПО АРХИВИРОВАННЫМ -->
        <v-col
          cols="12"
          class="py-0 mt-2"
        >
          <h5 class="mt-5">
            {{ $t('upload.bundles.filters.archived') }}
          </h5>
          <v-checkbox
            v-model="filters.showArchived"
            :label="$t('upload.bundles.filters.showArchivedAccounts')"
            @change="$store.dispatch('bundles/setSpecificFilter', {filter: 'showArchived', data: $event ? true : false});"
          />
        </v-col>
      </v-row>
    </div>
  </v-navigation-drawer>
</template>

<script>
import { mapGetters }                   from 'vuex';  

export default {
  name: 'UploadBundlesFiltersBar',

  components: {

  },

  props: {
    show: {
      type: Boolean,
      default: false,
    }
  },

  data() {
    return {
      loadCurrentLevelTagsInterval: 0
    };
  },

  computed: {
    ...mapGetters({
      bundles: 'bundles/bundles',
      filters: 'bundles/filters',
      currentLevelTags: 'tags/currentLevelTags'
    }),
  },

  created() {
    this.$store.dispatch('tags/loadCurrentLevelTags', 'bundles');
    this.loadCurrentLevelTagsInterval = setInterval(() => {
      this.$store.dispatch('tags/loadCurrentLevelTags', 'bundles');
    }, 60000);
  },

  beforeDestroy() {
    clearInterval(this.loadCurrentLevelTagsInterval);
  },

  methods: {
    async filterTags(tags) {
      if (tags && tags.length > 0) {
        this.$store.dispatch('bundles/clearSelected');
      }
      const data = {
        filter: 'tags',
        data: tags
      };
      await this.$store.dispatch('bundles/setSpecificFilter', data);
    }
  }
};
</script>