<template>
  <div>
    <!-- НАЗВАНИЕ -->
    <div
      style="font-size: 16px; cursor: pointer; color: #2196F3; text-decoration: underline;"
      @click="editList(item)"
    >
      {{ item.name }}
    </div>

    <!-- ИМЯ ПОЛЬЗОВАТЕЛЯ -->
    <div style="font-size: 12px; color: gray; line-height: 14px;">
      {{ $t('common.owner') + ": " + item.user.display_name || item.user.login }}
    </div>
  </div>
</template>

<script>
export default {
  name: 'UploadListsMainTableName',

  props: {
    item: {
      type: Object,
      default: () => ({})
    }
  },

  methods: {
    async editList(item) {
      let data = {
        type: item.type,
        name: item.name,
        items: item.items,
        shared_to_all: item.shared_to_all,
        permissions: item.permissions
      };
      await this.$store.dispatch('upload_lists/setDialogMode', 'edit');
      await this.$store.dispatch('upload_lists/setList', data);
      await this.$store.dispatch('upload_lists/setIdForEdit', this.item.id);
      this.$store.dispatch('upload_lists/openDialog', 'createList');
    }
  }
};
</script>