<script>
import { mapGetters } from 'vuex';

export default {
  name: 'UploadBundlesDialogsMainDialogCampaignsBudget',

  computed: {
    ...mapGetters({
      forMainDialog: 'bundles/forMainDialog',
    }),
  },

  methods: {
    setProperty(key, value) {
      this.$store.dispatch('bundles/setForMainDialogProperty', { key, value });
    },

    handleBlur(key) {
      if (this.forMainDialog[key] < 0) {
        this.setProperty(key, 0);
      }
    }
  }
};
</script>

