<template>
  <v-card class="wizard-box-shadow mt-4">
    <!-- НАЗВАНИЕ КАРТОЧКИ -->
    <v-card-title class="pb-3">
      {{ $t('tracker.conversions.time') }}
    </v-card-title>
    <!-- НАЗВАНИЕ КАРТОЧКИ КОНЕЦ -->

    <v-card-text>
      <v-row>
        <!-- ЗАПУСКАТЬ -->
        <col-title :title="$t('upload.wizard.start') + ':'" />
        <time-start />
        <!-- ЗАПУСКАТЬ КОНЕЦ -->

        <!-- ДАТА ЗАЛИВА -->
        <col-title :title="$t('upload.wizard.uploadDate') + ':'" />
        <time-upload-date />
        <!-- ДАТА ЗАЛИВА КОНЕЦ -->

        <!-- ПОСТАВИТЬ ОБЪЯВЛЕНИЯ НА ПАУЗУ -->
        <v-col
          cols="12"
          class="d-flex align-center pb-0"
        >
          <v-switch
            :input-value="job.pauseAds"
            :label="$t('upload.wizard.static.time.pauseAd')"
            class="pt-0 mt-0"
            dense
            hide-details
            @change="setProperty('pauseAds', $event)"
          />
          <v-tooltip
            bottom
            max-width="300"
          >
            <template #activator="{ on }">
              <v-icon
                :size="14"
                class="ml-2 cursor-pointer"
                v-on="on"
              >
                far fa-question-circle
              </v-icon>
            </template>
            <span>{{ $t('upload.mainDialog.budgetTypeHint') }}</span>
          </v-tooltip>
        </v-col>
        <!-- ПОСТАВИТЬ ОБЪЯВЛЕНИЯ НА ПАУЗУ КОНЕЦ -->

        <!-- ПАУЗА МЕЖДУ КАБИНЕТАМИ -->
        <v-col
          cols="12"
          class="d-flex align-center"
        >
          <v-switch
            :input-value="job.pauseBetweenAdAccounts"
            :label="$t('dialogs.cabs.attachCard.pauseBetweenCabs')"
            class="pt-0 mt-0"
            dense
            hide-details
            @change="setProperty('pauseBetweenAdAccounts', $event)"
          />

          <v-text-field
            v-show="job.pauseBetweenAdAccounts"
            :value="job.pauseBetweenAdAccountsValue"
            outlined
            dense
            hide-details
            type="number"
            min="0"
            class="centralize-append-outer wizard-stepper-pause-cabs-textfield"
            @blur="handleBlur('pauseBetweenAdAccountsValue')"
            @input="setProperty('pauseBetweenAdAccountsValue', $event)"
          >
            <template #append-outer>
              {{ $t('common.sec') }}
            </template>
          </v-text-field>

          <v-tooltip
            bottom
            max-width="300"
          >
            <template #activator="{ on }">
              <v-icon
                :size="14"
                class="ml-2 cursor-pointer"
                v-on="on"
              >
                far fa-question-circle
              </v-icon>
            </template>
            <span>{{ $t('upload.mainDialog.budgetTypeHint') }}</span>
          </v-tooltip>
        </v-col>
        <!-- ПАУЗА МЕЖДУ КАБИНЕТАМИ КОНЕЦ -->
      </v-row>
    </v-card-text>
  </v-card>
</template>

<script>
import { mapGetters } from 'vuex';

import ColTitle       from '../UploadWizardMainStepperSettingsColTitle.vue';

import TimeStart      from './UploadWizardMainStepperSettingsTimeStart.vue';
import TimeUploadDate from './UploadWizardMainStepperSettingsTimeUploadDate.vue';

export default {
  name: 'UploadWizardMainStepperSettingsTime',

  components: {
    ColTitle,
    TimeStart,
    TimeUploadDate
  },

  computed: {
    ...mapGetters({
      job: 'upload_wizard/job',
    }),
  },

  methods: {
    setProperty(key, value) {
      this.$store.dispatch('upload_wizard/setJobProperty', { key, value });
    },

    handleBlur(key) {
      if (this.job[key] < 0) {
        this.setProperty(key, 0);
      }
    }
  }
};
</script>

<style>
.wizard-stepper-pause-cabs-textfield {
  max-width: 120px;
  margin-left: 8px !important;
}
</style>