<template>
  <v-dialog
    :value="true"
    width="850"
    @click:outside="$emit('close')"
    @keydown.esc="$emit('close')"
  >
    <v-card>
      <!-- ЗАГОЛОВОК ДИАЛОГА -->
      <v-card-title class="headline">
        {{ $t('upload.mainDialog.selectMacrosForTheAdsetName') }}

        <v-spacer />
        <!-- КРЕСТИК -->
        <v-btn
          icon
          small
          @click="$emit('close')"
        >
          <v-icon>
            clear
          </v-icon>
        </v-btn>
        <!-- КРЕСТИК КОНЕЦ -->
      </v-card-title>
      <!-- ЗАГОЛОВОК ДИАЛОГА -->

      <v-card-text>
        <div class="create-bundle-a-macro mb-3">
          {{ $t('upload.mainDialog.aMacroAdset') }}
        </div>
        <label>
          <span class="font-16">
            {{ $t('common.adsetName') }}
          </span>
          <v-text-field
            v-model="adsetName"
            outlined
            dense
            hide-details
            class="mt-1 mb-3"
          />
        </label>

        <div
          v-for="{ macro, locale } in adsetMacros"
          :key="`adsets-macro-dialog-${macro}`"
          class="create-bundle-macros"
        >
          <v-btn
            x-small
            outlined
            min-width="180"
            :color="macroBtnColor(macro)"
            @click="handleMacroClick(macro)"
          >
            {{ macro }}
          </v-btn>
          <span>{{ ' - ' + $t(`upload.bundles.mainDialog.macros.${locale}`) }}</span>
        </div>
      </v-card-text>

      <v-divider />

      <!-- ДЕЙСТВИЯ ДИАЛОГА -->
      <v-card-actions>
        <!-- СОХРАНИТЬ -->
        <v-btn
          color="primary"
          @click="save"
        >
          {{ $t('common.save') }}
        </v-btn>
        <!-- СОХРАНИТЬ КОНЕЦ -->

        <v-spacer />

        <!-- ОТМЕНА -->
        <v-btn
          color="primary"
          outlined
          class="box-shadow-none"
          @click="$emit('close')"
        >
          {{ $t('common.cancel') }}
        </v-btn>
        <!-- ОТМЕНА КОНЕЦ -->
      </v-card-actions>
      <!-- ДЕЙСТВИЯ ДИАЛОГА КОНЕЦ -->
    </v-card>
  </v-dialog>
</template>

<script>
import { adsetMacros } from '@/constants/bundles/macro';
import { mapGetters }  from 'vuex';

export default {
  name: 'UploadBundlesDialogsMainDialogAdsetsMainAvailableMacros',

  props: {
    show: {
      type: Boolean,
      default: false
    }
  },

  data() {
    return {
      adsetName: '',
      adsetMacros
    };
  },

  computed: {
    ...mapGetters({
      forMainDialog: 'bundles/forMainDialog',
    }),
  },

  created() {
    this.adsetName = this.forMainDialog.adset_name;
  },

  methods: {
    macroBtnColor(macro) {
      if (this.adsetName.includes(macro)) {
        return 'primary';
      }

      return 'grey';
    },

    handleMacroClick(macro) {
      if (this.adsetName.includes(macro)) {
        this.adsetName = this.adsetName.toString().replace(macro, '');
      } else {
        this.adsetName += macro;
      }
    },

    async save() {
      await this.$store.dispatch('bundles/setForMainDialogProperty', {
        key: 'adset_name',
        value: this.adsetName
      });
      await this.$emit('close');
    }
  }
};
</script>