<template>
  <v-card class="create-bundle-box-shadow mt-4">
    <!-- ДИАЛОГИ -->
    <dialog-available-macros
      v-if="dialogs.availableMacros"
      @close="dialogs.availableMacros=false"
    />
    <!-- ДИАЛОГИ КОНЕЦ -->

    <!-- НАЗВАНИЕ КАРТОЧКИ -->
    <v-card-title class="pb-3">
      {{ $t('upload.mainDialog.main') }}
    </v-card-title>
    <!-- НАЗВАНИЕ КАРТОЧКИ КОНЕЦ -->

    <v-card-text>
      <!-- НАЗВАНИЕ АДСЕТА -->
      <label
        class="font-16"
        for="mainAdsetName"
      >
        {{ $t('common.adsetName') }}
      </label>
      <div class="d-flex align-center mt-1">
        <v-text-field
          id="mainAdsetName"
          :value="forMainDialog.adset_name"
          outlined
          dense
          hide-details
          @input="setProperty('adset_name', $event)"
        />
        <v-btn
          color="primary"
          text
          class="ml-2"
          @click="dialogs.availableMacros=true"
        >
          {{ $t('common.macro') }}
        </v-btn>
      </div>
      <!-- НАЗВАНИЕ АДСЕТА КОНЕЦ -->
    </v-card-text>
  </v-card>
</template>

<script>
import { mapGetters }        from 'vuex';

import DialogAvailableMacros from './UploadBundlesDialogsMainDialogAdsetsMainAvailableMacros.vue';

export default {
  name: 'UploadBundlesDialogsMainDialogAdsetsMain',

  components: {
    DialogAvailableMacros
  },

  data() {
    return {
      dialogs: {
        availableMacros: false,
      }
    };
  },

  computed: {
    ...mapGetters({
      forMainDialog: 'bundles/forMainDialog',
    }),
  },

  methods: {
    setProperty(key, value) {
      this.$store.dispatch('bundles/setForMainDialogProperty', { key, value });
    }
  }
};
</script>

