<script>
import {mapGetters}         from 'vuex';

import FiltersBar           from './UploadBundlesFiltersBar';
import TopbarActions        from './UploadBundlesTopbarActions';

export default {
  name: 'UploadBundlesTopbar',

  components: {
    FiltersBar,
    TopbarActions
  },

  data() {
    return {

    };
  },

  computed: {
    ...mapGetters({
      dialogs: 'bundles/dialogs',
      filters: 'bundles/filters'
    }),

    activeFiltersCount() {
      let count = 0;

      if (!this.filters) return 0;

      if (
        typeof this.filters.tags !== 'undefined' &&
        this.filters.tags &&
        this.filters.tags.length > 0) count++;

      if ( 
        typeof this.filters.showArchived !== 'undefined' && 
        this.filters.showArchived
      ) count++;

      return count;
    }
  },
  methods: {
    async filterSearch(search) {
      if (search && search.length > 0) {
        this.$store.dispatch('bundles/clearSelected');
      }
      this.searchText = search;
      const data = {
        filter: 'search',
        data: search
      };
      setTimeout(async () => {
        if (search === this.searchText) {
          await this.$store.dispatch('bundles/setSpecificFilter', data);
        }  
      }, 500);
    },

    async createBundle() {
      await this.$store.dispatch('bundles/setMainDialogMode', 'create');
      await this.$store.dispatch('bundles/setForMainDialog');
      await this.$store.dispatch('bundles/openDialog', 'mainDialog');
    }
  }
};
</script>
