<template>
  <div style="padding: 8px;">
    <dialog-multiple-assign-tags v-if="dialogs.multipleAssignTags" />
    <dialog-multiple-share v-if="dialogs.multipleShare" />
    <main-dialog v-if="dialogs.mainDialog" />

    <topbar />
    <main-table />
  </div>
</template>

<script>
import DialogMultipleAssignTags from '@/components/upload/bundles/UploadBundlesDialogMultipleAssignTags.vue';
import DialogMultipleShare      from '@/components/upload/bundles/UploadBundlesDialogMultipleShare.vue';
import MainTable                from '@/components/upload/bundles/UploadBundlesMainTable.vue';
import Topbar                   from '@/components/upload/bundles/UploadBundlesTopbar.vue';
import MainDialog               from '@/components/upload/bundles/dialogs/mainDialog/UploadBundlesDialogsMainDialog.vue';
import { mapGetters }           from 'vuex';

export default {
  name: 'Bundles',

  components: {
    DialogMultipleAssignTags,
    DialogMultipleShare,
    MainDialog,
    MainTable,
    Topbar
  },

  data() {
    return {
      updateInterval: null
    };
  },

  computed: {
    ...mapGetters({
      dialogs: 'bundles/dialogs',
    })
  },

  created() {
    this.$store.dispatch('bundles/loadBundles');
    this.$store.dispatch('bundles/loadFilters');
    this.updateInterval = setInterval(() => {
      this.$store.dispatch('bundles/loadBundles');
    }, 60000);
  },

  beforeDestroy() {
    clearInterval(this.updateInterval);
  },
};
</script>