<template>
  <v-col cols="10">
    <v-btn-toggle
      :value="job.budgetLevel"
      color="primary"
      mandatory
      dense
      borderless
      @change="setProperty('budgetLevel', $event)"
    >
      <v-btn
        value="campaign"
        small
        v-text="$t('common.campaign')"
      />
      <v-btn
        value="adset"
        small
        v-text="$t('common.adset')"
      />
    </v-btn-toggle>
    <v-tooltip
      bottom
      max-width="300"
    >
      <template #activator="{ on }">
        <v-icon
          :size="14"
          class="ml-2 cursor-pointer"
          v-on="on"
        >
          far fa-question-circle
        </v-icon>
      </template>
      <span>{{ $t('upload.mainDialog.budgetTypeHint') }}</span>
    </v-tooltip>
  </v-col>
</template>

<script>
import moment         from 'moment';
import { mapGetters } from 'vuex';

export default {
  name: 'UploadWizardMainStepperSettingsBudgetAndBidBudgetLevel',

  data() {
    return {
      moment
    };
  },

  computed: {
    ...mapGetters({
      job: 'upload_wizard/job',
    }),
  },

  methods: {
    setProperty(key, value) {
      this.$store.dispatch('upload_wizard/setJobProperty', { key, value });
    },
  }
};
</script>