<template>
  <v-card class="create-bundle-box-shadow">
    <!-- НАЗВАНИЕ КАРТОЧКИ -->
    <v-card-title class="pb-0">
      {{ $t('upload.objectives.campaignGoal') }}
    </v-card-title>
    <!-- НАЗВАНИЕ КАРТОЧКИ КОНЕЦ -->

    <v-card-text>
      <v-row>
        <!-- СТОЛБЦЫ ЦЕЛЕЙ -->
        <v-col
          v-for="objectiveType in objectiveTypes"
          :key="`UploadBundlesDialogsMainDialogCampaignsObjectives-objectiveType-${objectiveType.name}`"
          cols="10"
          offset="1"
          sm="4"
          offset-sm="0"
        >
          <!-- НАЗВАНИЕ СТОЛБЦА -->
          <div class="mb-2 font-16">
            {{ $t(`upload.objectives.${objectiveType.name}`) }}
          </div>
          <!-- НАЗВАНИЕ СТОЛБЦА КОНЕЦ -->

          <!-- ЦЕЛИ -->
          <div
            v-for="objective in objectiveType.objectives"
            :key="`UploadBundlesDialogsMainDialogCampaignsObjectives-objective-${objective.value}`"
            class="cursor-pointer"
            @click="handleObjectiveClick(objective.value)"
          >
            <v-alert
              dense
              color="primary"
              :text="!isSelected(objective.value)"
              :dark="isSelected(objective.value)"
            >
              <v-row align="center">
                <!-- НАЗВАНИЕ ЦЕЛИ -->
                <v-col class="grow py-0">
                  {{ $t(`upload.objectives.${objective.name}`) }}
                </v-col>
                <!-- НАЗВАНИЕ ЦЕЛИ КОНЕЦ -->

                <!-- СПРАВКА -->
                <v-col class="shrink py-0">
                  <v-tooltip
                    bottom
                    max-width="300"
                  >
                    <template #activator="{ on }">
                      <v-icon
                        :size="14"
                        :color="questionIconColor(objective.value)"
                        v-on="on"
                      >
                        far fa-question-circle
                      </v-icon>
                    </template>
                    <span>{{ $t(`upload.objectives.hints.${objective.name}`) }}</span>
                  </v-tooltip>
                </v-col>
                <!-- СПРАВКА КОНЕЦ -->
              </v-row>
            </v-alert>
          </div>
          <!-- ЦЕЛИ КОНЕЦ -->
        </v-col>
        <!-- СТОЛБЦЫ ЦЕЛЕЙ КОНЕЦ -->
      </v-row>
    </v-card-text>
  </v-card>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: 'UploadBundlesDialogsMainDialogCampaignsObjectives',

  data() {
    return {
      objectiveTypes: [
        {
          name: 'recognition',
          objectives: [
            {
              name: 'coverage',
              value: 'REACH'
            }
          ]
        },
        {
          name: 'consideration',
          objectives: [
            {
              name: 'traffic',
              value: 'LINK_CLICKS'
            },
            {
              name: 'involvement',
              value: 'POST_ENGAGEMENT'
            },
            {
              name: 'app_installs',
              value: 'APP_INSTALLS'
            },
            {
              name: 'likes',
              value: 'PAGE_LIKES'
            }
          ]
        },
        {
          name: 'conversion',
          objectives: [
            {
              name: 'conversions',
              value: 'CONVERSIONS'
            },
            {
              name: 'targetViews',
              value: 'CONVERSIONS_LANDING_PAGE_VIEWS'
            }
          ]
        }
      ],
    };
  },

  computed: {
    ...mapGetters({
      forMainDialog: 'bundles/forMainDialog',
    }),
  },

  methods: {
    isSelected(value) {
      return this.forMainDialog.objective === value;
    },

    questionIconColor(value) {
      if (this.forMainDialog.objective === value) {
        return 'white';
      }
      return 'primary';
    },

    handleObjectiveClick(value) {
      this.$store.dispatch('bundles/setForMainDialogProperty', {
        key: 'objective',
        value
      });
    }
  }
};
</script>

