<template>
  <v-row class="topbar-row">
    <!-- ЛЕВАЯ ЧАСТЬ -->
    <v-col
      cols="12"
      sm="9"
    >
      <!-- ДЕЙСТВИЯ -->
      <div>
        <topbar-actions />
      </div>

      <div
        style="float: right; width: 134px; text-align: center;"
        class="pt-0 pb-2 mr-4"
      >
        <v-menu
          offset-y
          close-on-click
        >
          <template v-slot:activator="{ on }">
            <!-- КНОПКА СОЗДАТЬ СПИСОК -->
            <v-btn
              small
              text
              color="primary"
              style="margin-top: 2px;"
              class="white--text"
              v-on="on"
            >
              <v-icon
                :size="18"
                class="mr-1"
              >
                add
              </v-icon>
              {{ $t('common.createList') }}
            </v-btn>
          </template>
          <v-list>
            <!-- ГЕО -->
            <v-list-item
              dense
              @click="createList('geo')"
            >
              <v-list-item-title>{{ $t('common.geo') }}</v-list-item-title>
            </v-list-item>

            <!-- ИНТЕРЕСЫ -->
            <v-list-item
              dense
              @click="createList('interests')"
            >
              <v-list-item-title>{{ $t('common.interests') }}</v-list-item-title>
            </v-list-item>

            <!-- ПОВЕДЕНИЕ -->
            <v-list-item
              dense
              @click="createList('behaviors')"
            >
              <v-list-item-title>{{ $t('common.behaviors') }}</v-list-item-title>
            </v-list-item>
          </v-list>
        </v-menu>
      </div>

      <!-- ФИЛЬТР ПО ГЕО -->
      <div
        style="float: right;"
        class="pt-0 pb-2"
      >
        <v-switch
          v-model="filters.geo"
          :label="$t('common.geo')"
          class="mt-0 mr-6"
          dense
          hide-details
          @change="$store.dispatch('upload_lists/setSpecificFilter', {filter: 'geo', data: $event ? true : false});"
        />
      </div>

      <!-- ФИЛЬТР ПО ИНТЕРЕСАМ -->
      <div
        style="float: right;"
        class="pt-0 pb-2"
      >
        <v-switch
          v-model="filters.interests"
          :label="$t('common.interests')"
          class="mt-0 mr-6"
          dense
          hide-details
          @change="$store.dispatch('upload_lists/setSpecificFilter', {filter: 'interests', data: $event ? true : false});"
        />
      </div>

      <!-- ФИЛЬТР ПО ПОВЕДЕНИЮ -->
      <div
        style="float: right;"
        class="pt-0 pb-2"
      >
        <v-switch
          v-model="filters.behaviors"
          :label="$t('common.behaviors')"
          class="mt-0 mr-6"
          dense
          hide-details
          @change="$store.dispatch('upload_lists/setSpecificFilter', {filter: 'behaviors', data: $event ? true : false});"
        />
      </div>
    </v-col>

    <!-- ПРАВАЯ ЧАСТЬ -->
    <v-col
      cols="12"
      sm="3"
    >
      <v-row>
        <!-- ПОИСК ПО НАЗВАНИЮ -->
        <v-col
          cols="12"
          class="pt-0 pb-2"
        >
          <v-text-field
            dense
            clearable
            solo
            :label="$t('filters.searchByName')"
            single-line
            prepend-inner-icon="fas fa-search"
            hide-details
            :value="filters.search"
            @input="search"
          />
        </v-col>
      </v-row>
    </v-col>
  </v-row>
</template>

<script>
import {mapGetters}         from 'vuex';

import TopbarActions        from './UploadListsTopbarActions';

export default {
  name: 'UploadListsTopbar',
  
  components: {
    TopbarActions
  },

  data() {
    return {
      searchText: '',
    };
  },

  computed: {
    ...mapGetters({
      dialogs: 'upload_lists/dialogs',
      loading: 'upload_lists/loading',
      filters: 'upload_lists/filters',
    }),
  },

  methods: {
    search(search) {
      this.searchText = search;
      const data = {
        filter: 'search',
        data: search
      };
      setTimeout(async () => {
        if (search === this.searchText) {
          await this.$store.dispatch('upload_lists/setSpecificFilter', data);
        }
      }, 500);
    },

    async createList(type) {
      await this.$store.dispatch('upload_lists/setDialogMode', 'create');
      await this.$store.dispatch('upload_lists/setListType', type);
      this.$store.dispatch('upload_lists/openDialog', 'createList');
    }
  }
};
</script>