<template>
  <v-col
    cols="10"
    class="d-flex align-center"
  >
    <v-text-field
      :value="job.budget"
      outlined
      dense
      hide-details
      type="number"
      min="0"
      class="centralize-append-outer wizard-stepper-budget-textfield"
      @blur="handleBlur('budget')"
      @input="setProperty('budget', $event)"
    >
      <template #append-outer>
        USD
      </template>
    </v-text-field>

    <v-tooltip
      bottom
      max-width="300"
    >
      <template #activator="{ on }">
        <v-icon
          :size="14"
          class="cursor-pointer ml-2"
          v-on="on"
        >
          far fa-question-circle
        </v-icon>
      </template>
      <span>{{ $t('common.randomizeHint') }}</span>
    </v-tooltip>

    <v-switch
      :input-value="job.randomizeBudget"
      :label="$t('common.randomize')"
      class="pt-0 mt-0 ml-4"
      dense
      hide-details
      @change="setProperty('randomizeBudget', $event)"
    />

    <v-text-field
      v-if="job.randomizeBudget"
      :value="job.budgetRandom"
      outlined
      dense
      hide-details
      type="number"
      min="0"
      class="centralize-append-outer centralize-prepend-outer wizard-stepper-budget-textfield ml-2"
      @blur="handleBlur('budgetRandom')"
      @input="setProperty('budgetRandom', $event)"
    >
      <template #prepend>
        &plusmn;
      </template>
      <template #append-outer>
        %
      </template>
    </v-text-field>
    <v-tooltip
      v-if="job.randomizeBudget"
      bottom
      max-width="300"
    >
      <template #activator="{ on }">
        <v-icon
          :size="14"
          class="ml-2 cursor-pointer"
          v-on="on"
        >
          far fa-question-circle
        </v-icon>
      </template>
      <span>{{ $t('common.randomizeHint') }}</span>
    </v-tooltip>
  </v-col>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: 'UploadWizardMainStepperSettingsBudgetAndBidBudget',

  computed: {
    ...mapGetters({
      job: 'upload_wizard/job',
    }),
  },

  methods: {
    setProperty(key, value) {
      this.$store.dispatch('upload_wizard/setJobProperty', { key, value });
    },

    handleBlur(key) {
      if (this.job[key] < 0) {
        this.setProperty(key, 0);
      }
    }
  }
};
</script>

<style>
.wizard-stepper-budget-textfield {
  max-width: 120px;
}
</style>