<template>
  <v-card class="create-bundle-box-shadow mt-4">
    <!-- ДИАЛОГИ -->
    <dialog-available-macros
      v-if="dialogs.availableMacros"
      @close="dialogs.availableMacros=false"
    />
    <!-- ДИАЛОГИ КОНЕЦ -->

    <!-- НАЗВАНИЕ КАРТОЧКИ -->
    <v-card-title class="pb-3">
      {{ $t('upload.mainDialog.main') }}
    </v-card-title>
    <!-- НАЗВАНИЕ КАРТОЧКИ КОНЕЦ -->

    <v-card-text>
      <!-- НАЗВАНИЕ СВЯЗКИ -->
      <label>
        <span class="font-16">
          {{ $t('common.bundleNameInDolphin') }}
        </span>
        <v-text-field
          :value="forMainDialog.name"
          outlined
          dense
          hide-details
          class="mt-1 mb-3"
          @input="setProperty('name', $event)"
        />
      </label>
      <!-- НАЗВАНИЕ СВЯЗКИ КОНЕЦ -->
      
      <!-- НАЗВАНИЕ КАМПАНИИ В FACEBOOK -->
      <label
        class="font-16"
        for="campaignNameInFacebook"
      >
        {{ $t('common.campaignNameInFacebook') }}
      </label>
      <div class="d-flex align-center mt-1">
        <v-text-field
          id="campaignNameInFacebook"
          :value="forMainDialog.campaign_name"
          outlined
          dense
          hide-details
          @input="setProperty('campaign_name', $event)"
        />
        <v-btn
          color="primary"
          text
          class="ml-2"
          @click="dialogs.availableMacros=true"
        >
          {{ $t('common.macro') }}
        </v-btn>
      </div>
      <!-- НАЗВАНИЕ КАМПАНИИ В FACEBOOK КОНЕЦ -->
    </v-card-text>
  </v-card>
</template>

<script>
import { mapGetters }        from 'vuex';

import DialogAvailableMacros from './UploadBundlesDialogsMainDialogCampaignsMainAvailableMacros.vue';

export default {
  name: 'UploadBundlesDialogsMainDialogCampaignsMain',

  components: {
    DialogAvailableMacros
  },

  data() {
    return {
      dialogs: {
        availableMacros: false,
      }
    };
  },

  computed: {
    ...mapGetters({
      forMainDialog: 'bundles/forMainDialog',
    }),
  },

  methods: {
    setProperty(key, value) {
      this.$store.dispatch('bundles/setForMainDialogProperty', { key, value });
    }
  }
};
</script>

