<template>
  <div class="my-2">
    <!-- НАЗВАНИЕ -->
    <div
      v-if="editableBundle"
      style="cursor: pointer; color: #2196F3; text-decoration: underline; word-break: break-all;"
      @click="editBundle(bundle)"
    >
      {{ bundle.name }}
    </div>
    <div
      v-else
      style="word-break: break-all;"
    >
      {{ bundle.name }}
    </div>
    <div v-if="bundle.archived === 1">
      <v-chip 
        class="mr-1"
        x-small
      >
        {{ $t('common.archived') }}
      </v-chip>
    </div>

    <!-- ЦЕЛЬ И СОБЫТИЕ ПИКСЕЛЯ -->
    <v-chip 
      class="mt-1 my-1"
      color="primary"
      outlined
      small
      label
    > 
      {{ $t(`upload.objectives.${bundle.objective.toLowerCase()}`) }}
    </v-chip>
    <v-chip 
      v-if="bundle.objective === 'CONVERSIONS'" 
      color="primary"
      outlined
      small
      label
      class="ml-1 my-1"
    > 
      {{
        $te(`upload.pixel_events.${bundle.pixel_event.toLowerCase()}`)
          ? $t(`upload.pixel_events.${bundle.pixel_event.toLowerCase()}`) 
          : bundle.pixel_event 
      }}
    </v-chip>

    <!-- ВЛАДЕЛЕЦ -->
    <div style="font-size: 11px; color: gray;">
      <span
        class="d-block"
        style="line-height: 14px;"
      >
        {{ $t('common.owner') }}:
        {{ 
          bundle.user 
            ? bundle.user.display_name ? bundle.user.display_name : bundle.user.login
            : '-'
        }}
      </span>
    </div>

    <!-- ТЕГИ -->
    <div v-if="bundle.tags">
      <v-chip
        v-for="tag in bundle.tags"
        :key="`bundle-${bundle.id}-tag-${tag}`"
        class="mr-1"
        color="primary"
        outlined
        x-small
        label
      >
        {{ tag[0] }}
      </v-chip>
    </div>
  </div>
</template>

<script>
import {mapGetters}   from 'vuex';

export default {
  name: 'AccountsMainTableInfo',

  props: {
    bundle: {
      type: Object,
      default: () => ({})
    }
  },

  computed: {
    ...mapGetters({
      profile: 'main/profile',
    }),

    editableBundle() {
      if (this.bundle && this.bundle.user && this.bundle.user.id === this.profile.id) {
        return true;
      } else if (this.bundle &&
        this.profile &&
        this.bundle.user.id !== this.profile.id &&
        this.bundle.permissions.includes(`${this.profile.id}-actions`)
      ) {
        return true;
      }

      return false;
    }
  },

  methods: {
    async editBundle(bundle) {
      await this.$store.dispatch('bundles/setMainDialogMode', 'edit');
      await this.$store.dispatch('bundles/setForMainDialog', this.$_.cloneDeep(bundle));
      await this.$store.dispatch('bundles/openDialog', 'mainDialog');
    }
  }

};
</script>
