<template>
  <div style="padding: 8px;">
    <dialog-create-list v-if="dialogs.createList" />
    <topbar />
    <main-table />
  </div>
</template>

<script>
import DialogCreateList                                    from '@/components/upload/lists/UploadListsDialogCreateList.vue';
import MainTable                                           from '@/components/upload/lists/UploadListsMainTable.vue';
import Topbar                                              from '@/components/upload/lists/UploadListsTopbar.vue';
import {mapGetters}                                        from 'vuex';

export default {
  name: 'UploadLists',

  components: {
    DialogCreateList,
    MainTable,
    Topbar
  },

  computed: {
    ...mapGetters({
      dialogs: 'upload_lists/dialogs'
    })
  },

  async created() {
    await this.$store.dispatch('upload_lists/loadLists');
    this.$store.dispatch('upload_lists/loadFilters');
  }
};
</script>