<template>
  <v-tabs
    centered
    grow
    :value="tabNumber"
    :height="32"
  >
    <!-- СВЯЗКИ -->
    <v-tab
      key="tab-bundles"
      :value="0"
      @click="selectTab('bundles')"
    >
      {{ $t('upload.tabs.bundles') }}
    </v-tab>

    <!-- ЗАЛИТЬ СВЯЗКУ -->
    <v-tab 
      key="tab-upload-wizard"
      :value="1"
      @click="selectTab('wizard')"
    >
      {{ $t('upload.tabs.wizard') }}
    </v-tab>

    <!-- ОЧЕРЕДЬ ЗАЛИВКИ -->
    <v-tab
      key="tab-jobs"
      @click="selectTab('jobs')"
    >
      {{ $t('upload.tabs.jobs') }}
    </v-tab>

    <!-- СПИСКИ -->
    <v-tab
      key="tab-upload-lists"
      @click="selectTab('lists')"
    >
      {{ $t('upload.tabs.lists') }}
    </v-tab>
  </v-tabs>
</template>

<script>
import {mapGetters} from 'vuex';

export default {
  name: 'UploadTabs',

  data: () => ({
    tabs: ['bundles', 'upload-wizard', 'jobs', 'upload-lists'],
  }),

  computed: {
    ...mapGetters({
      activeTab: 'upload/activeTab',
    }),

    tabNumber() {
      if (typeof this.$route.params.tab === 'undefined' || this.$route.params.tab === 'bundles') {
        return 0;
      } else if (this.$route.params.tab === 'wizard') {
        return 1; 
      } else if (this.$route.params.tab === 'jobs') {
        return 2; 
      } else if (this.$route.params.tab === 'lists') {
        return 3; 
      }
      return 0;
    }
  },

  methods: {
    changeTab(tab) {
      this.$store.dispatch('upload/setActiveTab', {
        tab: tab,
        component: this.tabs[tab]
      });
    },

    selectTab(tab) {
      // if (this.$route.params.tab === 'wizard' && tab !== 'wizard') {
      //   if (!confirm(this.$t('common.dataReset'))) {
      //     this.$forceUpdate();
      //     return;
      //   }
      // this.$route.params.tab === tab ? true : this.$router.push({path: `/upload/${tab}`, params: {tab: tab}});
      // }
      return this.$route.params.tab === tab ? true : this.$router.push({path: `/upload/${tab}`, params: {tab: tab}});
    }
  }
};
</script>