<template>
  <div class="pb-3">
    <!-- АВТО-ПЛЕЙСМЕНТЫ -->
    <div class="d-flex align-center mb-3">
      <v-switch
        :input-value="forMainDialog.auto_placement"
        :label="$t('common.autoPlacements')"
        class="pt-0 mt-0"
        dense
        hide-details
        @change="setProperty('auto_placement', $event)"
      />
      <v-tooltip
        bottom
        max-width="300"
      >
        <template #activator="{ on }">
          <v-icon
            :size="14"
            class="ml-2 cursor-pointer"
            v-on="on"
          >
            far fa-question-circle
          </v-icon>
        </template>
        <span>{{ $t('common.autoPlacementsHint') }}</span>
      </v-tooltip>
    </div>
    <!-- АВТО-ПЛЕЙСМЕНТЫ КОНЕЦ -->

    <template v-if="!forMainDialog.auto_placement">
      <span class="font-16">
        {{ $t('common.platforms') }}
      </span>
      <div class="d-flex flex-wrap mb-3">
        <v-alert
          color="primary"
          dense
          text
          class="mb-0 mt-2"
        >
          <v-checkbox
            :input-value="checkboxFacebook"
            label="Facebook"
            hide-details
            :indeterminate="!checkboxFacebook && selectedItems.some(i => i.startsWith('facebook'))"
            class="mt-0 pt-0"
            @change="checkFacebook"
          />
        </v-alert>
        <v-alert
          color="primary"
          text
          dense
          class="mb-0 ml-2 mt-2"
        >
          <v-checkbox
            :input-value="checkboxInstagram"
            label="Instagram"
            hide-details
            :indeterminate="!checkboxInstagram && selectedItems.some(i => i.startsWith('instagram'))"
            class="mt-0 pt-0"
            @change="checkInstagram"
          />
        </v-alert>
        <v-alert
          color="primary"
          text
          dense
          class="mb-0 ml-2 mt-2"
        >
          <v-checkbox
            :input-value="checkboxAudienceNetwork"
            label="Audience Network"
            hide-details
            :indeterminate="!checkboxAudienceNetwork && selectedItems.some(i => i.startsWith('audience_network'))"
            class="mt-0 pt-0"
            @change="checkAudienceNetwork"
          />
        </v-alert>
        <v-alert
          color="primary"
          text
          dense
          class="mb-0 ml-2 mt-2"
        >
          <v-checkbox
            :input-value="checkboxMessenger"
            label="Messenger"
            hide-details
            :indeterminate="!checkboxMessenger && selectedItems.some(i => i.startsWith('messenger'))"
            class="mt-0 pt-0"
            @change="checkMessenger"
          />
        </v-alert>
      </div>
      <span class="font-16">
        {{ $t('common.placement') }}
      </span>
      <v-treeview
        v-model="selectedItems"
        selectable
        :items="items"
        selected-color="primary"
        color="warning"
        class="create-bundle-treeview"
      >
        <template #label="{item}">
          <template v-if="item.description">
            <v-list-item-content>
              <v-list-item-title>
                {{ item.name }}
              </v-list-item-title>
              <v-list-item-subtitle>
                {{ item.description }}
              </v-list-item-subtitle>
            </v-list-item-content>
          </template>
          <span v-else>
            {{ item.name }}
          </span>
        </template>
      </v-treeview>
    </template>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: 'UploadBundlesDialogsMainDialogAdsetsPlacements',

  data() {
    return {
      selectedItems: [],
      items: [
        {
          id: 'tapes',
          name: this.$t('upload.placements.tapes'),
          description: this.$t('upload.placements.tapesDesc'),
          locked: false,
          children: [
            { id: 'facebook_ribbon', name: this.$t('upload.placements.facebookRibbon') },
            { id: 'facebook_right_column', name: this.$t('upload.placements.facebookRightColumn') },
            { id: 'facebook_videotapes', name: this.$t('upload.placements.facebookVideotapes') },
            { id: 'facebook_marketplace', name: this.$t('upload.placements.facebookMarketplace') },
            { id: 'instagram_feed', name: this.$t('upload.placements.instagramFeed') },
            { id: 'instagram_interesting', name: this.$t('upload.placements.instagramInteresting') },
            { id: 'messenger_inbox', name: this.$t('upload.placements.messengerInbox') },
          ],
        },
        {
          id: 'reels_stories_and_videos',
          name: this.$t('upload.placements.reelsStoriesAndVideos'),
          description: this.$t('upload.placements.reelsStoriesAndVideosDesc'),
          locked: false,
          children: [
            { id: 'instagram_stories', name: this.$t('upload.placements.storiesOnInstagram') },
            { id: 'facebook_stories', name: this.$t('upload.placements.facebookStories') },
            { id: 'messenger_stories', name: this.$t('upload.placements.storiesInMessenger') },
          ],
        },
        {
          id: 'applications_and_websites',
          name: this.$t('upload.placements.applicationsAndWebsites'),
          description: this.$t('upload.placements.applicationsAndWebsitesDesc'),
          locked: false,
          children: [
            { id: 'audience_network_native_banner_interstitial_advertising',
              name: this.$t('upload.placements.audienceNetworkNativeBannerInterstitialAdvertising') },
            { id: 'audience_network_rewarded_video', name: this.$t('upload.placements.audienceNetworkRewardedVideo') },
            { id: 'audience_network_in_stream_video_ads', name: this.$t('upload.placements.audienceNetworkInStreamVideo') },
          ],
        },
        {
          id: 'facebook_in_steam_video',
          name: this.$t('upload.placements.facebookInSteamVideo'),
          description: this.$t('upload.placements.facebookInSteamVideoDesc'),
          locked: false,
        },
        {
          id: 'facebook_search_results',
          name: this.$t('upload.placements.facebookSearchResults'),
          description: this.$t('upload.placements.facebookSearchResultsDesc'),
          locked: false,
        },
        {
          id: 'messenger_promotional_messages',
          name: this.$t('upload.placements.promotionalMessagesInMessenger'),
          description: this.$t('upload.placements.promotionalMessagesInMessengerDesc'),
          locked: false,
        },
        {
          id: 'facebook_instant_articles',
          name: this.$t('upload.placements.instantArticlesFacebook'),
          description: this.$t('upload.placements.instantArticlesFacebookDesc'),
          locked: false,
        },
      ],
    };
  },

  computed: {
    ...mapGetters({
      forMainDialog: 'bundles/forMainDialog',
    }),

    checkboxFacebook() {
      let itemsCount = 0;

      for (const item of this.selectedItems) {
        if (item.startsWith('facebook')) itemsCount++;
      }

      return itemsCount === 8;
    },

    checkboxInstagram() {
      let itemsCount = 0;

      for (const item of this.selectedItems) {
        if (item.startsWith('instagram')) itemsCount++;
      }

      return itemsCount === 3;
    },

    checkboxAudienceNetwork() {
      let itemsCount = 0;

      for (const item of this.selectedItems) {
        if (item.startsWith('audience_network')) itemsCount++;
      }

      return itemsCount === 3;
    },

    checkboxMessenger() {
      let itemsCount = 0;

      for (const item of this.selectedItems) {
        if (item.startsWith('messenger')) itemsCount++;
      }

      return itemsCount === 3;
    },
  },

  methods: {
    setProperty(key, value) {
      this.$store.dispatch('bundles/setForMainDialogProperty', { key, value });
    },

    checkFacebook(value) {
      if (value) {
        const items = [
          'facebook_ribbon',
          'facebook_right_column',
          'facebook_videotapes',
          'facebook_marketplace',
          'facebook_stories',
          'facebook_in_steam_video',
          'facebook_search_results',
          'facebook_instant_articles'
        ];

        for (const item of items) {
          if (!this.selectedItems.includes(item)) {
            this.selectedItems.push(item);
          }
        }
      } else {
        this.selectedItems = this.selectedItems.filter(i => !i.startsWith('facebook'));
      }
    },

    checkInstagram(value) {
      if (value) {
        const items = [
          'instagram_feed',
          'instagram_interesting',
          'instagram_stories',
        ];

        for (const item of items) {
          if (!this.selectedItems.includes(item)) {
            this.selectedItems.push(item);
          }
        }
      } else {
        this.selectedItems = this.selectedItems.filter(i => !i.startsWith('instagram'));
      }
    },

    checkAudienceNetwork(value) {
      if (value) {
        const items = [
          'audience_network_native_banner_interstitial_advertising',
          'audience_network_rewarded_video',
          'audience_network_in_stream_video_ads',
        ];

        for (const item of items) {
          if (!this.selectedItems.includes(item)) {
            this.selectedItems.push(item);
          }
        }
      } else {
        this.selectedItems = this.selectedItems.filter(i => !i.startsWith('audience_network'));
      }
    },

    checkMessenger(value) {
      if (value) {
        const items = [
          'messenger_inbox',
          'messenger_stories',
          'messenger_promotional_messages',
        ];

        for (const item of items) {
          if (!this.selectedItems.includes(item)) {
            this.selectedItems.push(item);
          }
        }
      } else {
        this.selectedItems = this.selectedItems.filter(i => !i.startsWith('messenger'));
      }
    },
  }
};
</script>

<style>
.create-bundle-treeview .v-list-item__title,
.create-bundle-treeview .v-list-item__subtitle,
.create-bundle-treeview .v-treeview-node__label {
  white-space: normal;
}
.create-bundle-treeview .v-list-item__subtitle {
  font-size: 12px;
}
.theme--light .create-bundle-treeview .v-list-item__subtitle {
  color: #666666;
}
.theme--dark .create-bundle-treeview .v-list-item__subtitle {
  color: #909090
}
</style>
