<template>
  <div class="upload-content">
    <tabs />
    <div class="upload-tab-content">
      <component :is="componentName" />
    </div>
  </div>
</template>

<script>
import Tabs             from '@/components/upload/UploadTabs.vue';
import Bundles          from '@/views/upload/Bundles.vue';
import Jobs             from '@/views/upload/UploadJobs.vue';
import Lists            from '@/views/upload/UploadLists.vue';
import Wizard           from '@/views/upload/UploadWizard.vue';

export default {
  name: 'Upload',

  components: {
    Bundles,
    Tabs,
    Wizard,
    Lists,
    Jobs
  },

  computed: {
    componentName() {
      if (typeof this.$route.params.tab !== 'undefined') {
        return this.$route.params.tab;
      }
      return 'bundles';
    }
  }
};
</script>