<template>
  <v-card class="create-bundle-box-shadow mt-4">
    <!-- НАЗВАНИЕ КАРТОЧКИ -->
    <v-card-title class="pb-3">
      {{ $t('upload.bundles.mainDialog.cardsTitles.geo') }}
    </v-card-title>
    <!-- НАЗВАНИЕ КАРТОЧКИ КОНЕЦ -->

    <v-card-text>
      <label class="font-16">
        {{ $t('upload.locations.locations') }}
        <v-select
          :value="forMainDialog.locations"
          :item-text="item => $t(`upload.locations.${item.text}`)"
          :item-value="item => item.value"
          :items="locations"
          outlined
          dense
          hide-details
          class="mt-1"
          @change="setProperty('locations', $event)"
        />
      </label>

      <geo-tree />
    </v-card-text>
  </v-card>
</template>

<script>
import locations      from '@/constants/bundles/locations';
import { mapGetters } from 'vuex';

import GeoTree        from './UploadBundlesDialogsMainDialogAdsetsGeoTree.vue';

export default {
  name: 'UploadBundlesDialogsMainDialogAdsetsGeo',

  components: {
    GeoTree
  },

  data() {
    return {
      locations
    };
  },

  computed: {
    ...mapGetters({
      forMainDialog: 'bundles/forMainDialog',
    }),
  },

  methods: {
    setProperty(key, value) {
      this.$store.dispatch('bundles/setForMainDialogProperty', { key, value });
    },
  }
};
</script>

