<template>
  <div>
    <!-- КНОПКА УДАЛИТЬ -->
    <div
      class="pt-0 pb-2"
      style="float: left;"
    >
      <v-tooltip bottom>
        <template v-slot:activator="{ on }">
          <v-btn
            small
            :disabled="selected.length === 0"
            color="primary"
            text
            style="min-width: 0; width: 30px; margin-top: 1px;"
            @click="deleteList"
            v-on="on"
          >
            <v-icon :size="12">
              fas fa-trash
            </v-icon>
          </v-btn>
        </template>
        <span>{{ $t('common.delete') }}</span>
      </v-tooltip>
    </div>

    <!-- ИНДИКАТОР -->
    <div
      class="pt-0 pb-2"
      style="float: left;"
    >
      <div
        class="body-2 ml-3"
        style="padding-bottom: 3px;"
        :style="{color: selected.length > 0 ? '' : 'gray', paddingTop: '5px'}"
      >
        {{ $t('common.selected') }}: {{ selected.length }}
      </div>
    </div>
  </div>
</template>

<script>
import {mapGetters} from 'vuex';

export default {
  name: 'UploadListsTopbarActions',

  computed: {
    ...mapGetters({
      loading: 'upload_lists/loading',
      selected: 'upload_lists/selected',
    })
  },
  methods: {
    deleteList() {
      if (!confirm(this.$t('common.confirmPlease'))) return;
      let data = this.selected.map(list => list.id).join(',');
      this.$store.dispatch('upload_lists/deleteList', data);
    },

    async clearSelected(context) {
      context.commit('SET_SELECTED', []);
    },
  }
};
</script>