<template>
  <div>
    <!-- РЕСТАРТ -->
    <div
      class="pt-0 pt-2"
      style="float: left;"
    >
      <v-tooltip bottom>
        <template v-slot:activator="{ on }">
          <v-btn
            small
            :disabled="selected.length === 0"
            color="primary"
            text
            style="min-width: 0; width: 30px; margin-top: 1px;"
            :loading="loading.restartJob"
            @click="restartJob"
            v-on="on"
          >
            <v-icon :size="16">
              replay
            </v-icon>
          </v-btn>
        </template>
        <span>{{ $t('common.restart') }}</span>
      </v-tooltip>
    </div>
    
    <!-- КНОПКА УДАЛИТЬ -->
    <div
      class="pt-0 pt-2"
      style="float: left;"
    >
      <v-tooltip bottom>
        <template v-slot:activator="{ on }">
          <v-btn
            small
            :disabled="selected.length === 0"
            color="primary"
            text
            style="min-width: 0; width: 30px; margin-top: 1px;"
            :loading="loading.deleteJob"
            @click="deleteJob"
            v-on="on"
          >
            <v-icon :size="12">
              fas fa-trash
            </v-icon>
          </v-btn>
        </template>
        <span>{{ $t('common.delete') }}</span>
      </v-tooltip>
    </div>

    <!-- ИНДИКАТОР -->
    <div
      class="pt-0 pt-2"
      style="float: left;"
    > 
      <div
        class="body-2 ml-3"
        style="padding-bottom: 3px;"
        :style="{color: selected.length > 0 ? '' : 'gray', paddingTop: '5px'}"
      >
        {{ $t('common.selected') }}: {{ selected.length }}
      </div>
    </div>
  </div>
</template>

<script>
import {mapGetters} from 'vuex';

export default {
  name: 'UploadJobsTopbarActions',

  data() {
    return {
      updateIntervalStatus: null,
    };
  },

  computed: {
    ...mapGetters({
      loading: 'upload_jobs/loading',
      selected: 'upload_jobs/selected',
    })
  },

  beforeDestroy() {
    clearInterval(this.updateIntervalStatus);
  },

  methods: {
    deleteJob() {
      if (!confirm(this.$t('common.confirmPlease'))) return;
      let data = this.selected.map(job => job.id).join(',');
      this.$store.dispatch('upload_jobs/deleteJob', data);
    },

    async restartJob() {
      if (!confirm(this.$t('common.confirmPlease'))) return;
      let data = this.selected.map(job => job.id).join(',');
      this.$store.dispatch('upload_jobs/restartJob', data);
    },
  },
};
</script>