<template>
  <div>
    <!-- ЛОГ КНОПКА -->
    <v-tooltip bottom>
      <template v-slot:activator="{ on }">
        <v-btn
          small
          color="primary"
          text
          icon
          style="min-width: 0; width: 30px; margin-top: 1px;"
          v-on="on"
          @click="openLog"
        >
          <v-icon :size="18">
            assignment
          </v-icon>
        </v-btn>
      </template>
      <span>{{ $t('common.logView') }}</span>
    </v-tooltip>     
  </div>
</template>

<script>
 
export default {
  name: 'UploadJobsMainTableLog',

  props: {
    item: {
      type: Object,
      default: () => ({})
    }
  },

  methods: {
    async openLog() {
      await this.$store.dispatch('upload_jobs/setJobForLog', this.item.id);
      this.$store.dispatch('upload_jobs/openDialog', 'mainTableLogDrawer');
    }
  }
};
</script>