<template>
  <v-card class="create-bundle-box-shadow mt-4">
    <!-- НАЗВАНИЕ КАРТОЧКИ -->
    <v-card-title class="pb-3">
      {{ $t('upload.bundles.mainDialog.cardsTitles.bidStrategies') }}
    </v-card-title>
    <!-- НАЗВАНИЕ КАРТОЧКИ КОНЕЦ -->

    <v-card-text>
      <!-- СТРАТЕГИЯ СТАВОК -->
      <label>
        <span class="font-16">
          {{ $t('upload.mainDialog.chooseAStrategy') }}
        </span>
        <v-select
          :value="forMainDialog.bid_strategy"
          :item-value="item => item.value"
          :items="bidStrategies"
          outlined
          dense
          hide-details
          @change="setBidStrategy"
        >
          <template #item="{item}">
            <v-list-item-content>
              <v-list-item-title>
                {{ $t(`upload.bid_strategies.${item.text}`) }}
              </v-list-item-title>
              <v-list-item-subtitle>
                {{ $t(`upload.bid_strategies.hints.${item.text}`) }}
              </v-list-item-subtitle>
            </v-list-item-content>
          </template>

          <template #selection="{item}">
            {{ $t(`upload.bid_strategies.${item.text}`) }}
          </template>

        </v-select>
      </label>
      <!-- СТРАТЕГИЯ СТАВОК КОНЕЦ -->
    </v-card-text>
  </v-card>
</template>

<script>
import bidStrategies  from '@/constants/bundles/bid_strategies';
import { mapGetters } from 'vuex';

export default {
  name: 'UploadBundlesDialogsMainDialogCampaignsBidStrategy',

  data() {
    return {
      bidStrategies
    };
  },

  computed: {
    ...mapGetters({
      forMainDialog: 'bundles/forMainDialog',
    }),
  },

  methods: {
    setBidStrategy(value) {
      this.$store.dispatch('bundles/setForMainDialogProperty', {
        key: 'bid_strategy',
        value
      });
    },
  }
};
</script>

