<template>
  <div>
    <v-card>
      <v-card-text style="padding: 0px 8px 0px 8px">
        <!-- ТАБЛИЦА -->
        <v-data-table
          :value="selected"
          :headers="cols"
          fixed-header
          :items="jobs.all"
          :items-per-page="50"
          :footer-props="{'items-per-page-options': [10, 30, 50, 100, -1]}"
          :height="innerHeight - 50 - 48 - 36"
          :sort-by="'id'"
          :sort-desc="true"
          show-select
          @input="selectJobs($event)"
        >
          <!-- ID -->
          <template #[`item.id`]="{item}">
            {{ item.id }}
          </template>

          <!-- LOG -->
          <template #[`item.log`]="{item}">
            <main-table-log :item="item" />
          </template>

          <!-- НАЗВАНИЕ СВЯЗКИ -->
          <template #[`item.bundle`]="{item}">
            {{ item.bundle.name }}
          </template>

          <!-- АККАУНТ -> КАБИНЕТ -->
          <template #[`item.adAccount`]="{item}">
            <main-table-ad-account :item="item" />
          </template>

          <!-- ИНФО -->
          <template #[`item.info`]="{item}">
            <main-table-info :item="item" />
          </template>

          <!-- ВРЕМЯ ЗАЛИВКИ -->
          <template #[`item.time`]="{item}">
            <span v-if="item.time_end && item.time_end !== 0">
              {{ moment(item.time_end).format("YYYY-MM-DD HH:mm") }}
            </span>
          </template>

          <!-- СТАТУС -->
          <template #[`item.status`]="{item}">
            <main-table-status :item="item" />
          </template>
        </v-data-table>
      </v-card-text>
    </v-card>
  </div>
</template>

<script>
import moment                                                    from 'moment';
import { mapFields }                                             from 'vuex-map-fields';
import {mapGetters}                                              from 'vuex';

import MainTableAdAccount                                        from './UploadJobsMainTableAdAccount';
import MainTableInfo                                             from './UploadJobsMainTableInfo';
import MainTableLog                                              from './UploadJobsMainTableLog';
import MainTableStatus                                           from './UploadJobsMainTableStatus';

export default {
  name: 'UploadJobsMainTable',

  components: {
    MainTableAdAccount,
    MainTableInfo,
    MainTableLog,
    MainTableStatus
  },

  data() {
    return {
      moment,
      cols: [
        {
          text: 'ID',
          value: 'id',
          width: 35
        },
        {
          text: '',
          value: 'log',
          width: 50
        },
        {
          text: this.$t('common.bundle'),
          value: 'bundle',
          width: 250,
        },
        {
          text: this.$t('common.adAccount'),
          value: 'adAccount',
          width: 250,
        },
        {
          text: this.$t('common.info'),
          value: 'info',
          // width: 200,
        },
        {
          text: this.$t('upload.wizard.uploadTime'),
          value: 'time',
          width: 200,
        },
        {
          text: this.$t('common.status'),
          value: 'status',
          width: 250,
        },
      ],
      updateJobsInterval: null,
      updateStatusesInterval: null,
    };
  },

  computed: {
    ...mapGetters({
      innerHeight: 'main/innerHeight',
      period: 'upload_jobs/period'
    }),
    ...mapFields('upload_jobs', [
      'jobs',
      'selected'
    ]),
  },

  created() {
    this.loadJobs();
    this.updateJobsInterval = setInterval(this.loadJobs, 60000);
  },

  beforeDestroy() {
    clearInterval(this.updateJobsInterval);
    this.updateJobsInterval = null;

    clearInterval(this.updateStatusesInterval);
    this.updateStatusesInterval = null;
  },

  methods: {
    async loadJobs() {
      await this.$store.dispatch('upload_jobs/setJobs', this.period);

      if (!this.updateStatusesInterval) {
        this.updateStatusesInterval = setInterval(() => {
          const ids = [];
          this.jobs.all.forEach(i => {
            if (this.checkStatus(i)) ids.push(i.id);
          });
          if (ids.length > 0) {
            this.$store.dispatch('upload_jobs/updateStatuses', ids);
          } else {
            clearInterval(this.updateStatusesInterval);
            this.updateStatusesInterval = null;
          }
        }, 2000);
      }
    },
    
    checkStatus(job) {
      return (parseInt(job.status, 10) !== 6 && parseInt(job.has_error, 10) !== 1) || (job.upload_start_time && job.upload_start_time > 0);
    },

    selectJobs(jobs) {
      this.$store.dispatch('upload_jobs/setSelectedJobs', jobs);
    }
  }

};
</script>

<style>
  .stat-table-bms div.v-data-table__wrapper > table > tbody > tr > td:nth-child(1),
  .stat-table-bms div.v-data-table__wrapper > table > thead > tr > th:nth-child(1) {
    position: sticky !important;
    position: -webkit-sticky !important;
    left: 0;
    z-index: 3;
  }

  .stat-table-bms div.v-data-table__wrapper > table > tbody > tr > td:nth-child(2),
  .stat-table-bms div.v-data-table__wrapper > table > thead > tr > th:nth-child(2) {
    position: sticky !important;
    position: -webkit-sticky !important;
    left: 48px;
    z-index: 3;
  }

  .stat-table-bms div.v-data-table__wrapper > table > tbody > tr > td:nth-child(3),
  .stat-table-bms div.v-data-table__wrapper > table > thead > tr > th:nth-child(3) {
    position: sticky !important;
    position: -webkit-sticky !important;
    left: 248px;
    z-index: 3;
  }

  .stat-table-bms div.v-data-table__wrapper > table > thead > tr > th:nth-child(1),
  .stat-table-bms div.v-data-table__wrapper > table > thead > tr > th:nth-child(2),
  .stat-table-bms div.v-data-table__wrapper > table > thead > tr > th:nth-child(3) {
    z-index: 4 !important;
  }
</style>
