<template>
  <v-col cols="10">
    <!-- СТРАТЕГИЯ СТАВОК -->
    <v-select
      :value="job.bidStrategy"
      :item-value="item => item.value"
      :items="bidStrategies"
      outlined
      dense
      hide-details
      @change="setProperty('bidStrategy', $event)"
    >
      <template #item="{item}">
        <v-list-item-content>
          <v-list-item-title>
            {{ $t(`upload.bid_strategies.${item.text}`) }}
          </v-list-item-title>
          <v-list-item-subtitle>
            {{ $t(`upload.bid_strategies.hints.${item.text}`) }}
          </v-list-item-subtitle>
        </v-list-item-content>
      </template>

      <template #selection="{item}">
        {{ $t(`upload.bid_strategies.${item.text}`) }}
      </template>
    </v-select>
    <!-- СТРАТЕГИЯ СТАВОК КОНЕЦ -->

    <div
      v-if="showBid"
      class="d-flex align-center mt-3"
    >
      <label for="jobBidAmount">
        {{ $t('common.bidAmount') }}
      </label>

      <v-text-field
        id="jobBidAmount"
        :value="job.bidAmount"
        outlined
        dense
        hide-details
        type="number"
        min="0"
        class="centralize-append-outer wizard-stepper-budget-textfield ml-2"
        @blur="handleBlur('bidAmount')"
        @input="setProperty('bidAmount', $event)"
      >
        <template #append-outer>
          USD
        </template>
      </v-text-field>

      <v-tooltip
        bottom
        max-width="300"
      >
        <template #activator="{ on }">
          <v-icon
            :size="14"
            class="cursor-pointer ml-2"
            v-on="on"
          >
            far fa-question-circle
          </v-icon>
        </template>
        <span>{{ $t('common.randomizeHint') }}</span>
      </v-tooltip>
    </div>
  </v-col>
</template>

<script>
import bidStrategies  from '@/constants/bundles/bid_strategies';
import { mapGetters } from 'vuex';

export default {
  name: 'UploadWizardMainStepperSettingsBudgetAndBidBidStrategies',

  data() {
    return {
      bidStrategies
    };
  },

  computed: {
    ...mapGetters({
      job: 'upload_wizard/job',
    }),

    showBid() {
      return ['LOWEST_COST_WITH_BID_CAP', 'COST_CAP'].includes(this.job.bidStrategy);
    }
  },

  methods: {
    setProperty(key, value) {
      this.$store.dispatch('upload_wizard/setJobProperty', { key, value });
    },

    handleBlur(key) {
      if (this.job[key] < 0) {
        this.setProperty(key, 0);
      }
    }
  }
};
</script>