  <v-card class="create-bundle-box-shadow mt-4">
    <!-- ДИАЛОГИ -->
    <dialog-available-macros
      v-if="dialogs.availableMacros"
      @close="dialogs.availableMacros=false"
    />
    <!-- ДИАЛОГИ КОНЕЦ -->

    <!-- НАЗВАНИЕ КАРТОЧКИ -->
    <v-card-title class="pb-3">
      {{ $t('upload.mainDialog.main') }}
    </v-card-title>
    <!-- НАЗВАНИЕ КАРТОЧКИ КОНЕЦ -->

    <v-card-text>
      <!-- НАЗВАНИЕ АДСЕТА -->
      <label
        class="font-16"
        for="mainAdName"
      >
        {{ $t('common.adName') }}
      </label>
      <div class="d-flex align-center mt-1 mb-3">
        <v-text-field
          id="mainAdName"
          :value="forMainDialog.ad_name"
          outlined
          dense
          hide-details
          @input="setProperty('ad_name', $event)"
        />
        <v-btn
          color="primary"
          text
          class="ml-2"
          @click="dialogs.availableMacros=true"
        >
          {{ $t('common.macro') }}
        </v-btn>
      </div>
      <!-- НАЗВАНИЕ АДСЕТА КОНЕЦ -->

      <!-- ЗАГОЛОВОК -->
      <div class="mb-3">
        <label>
          <span class="font-16">
            {{ $t('common.headline') }}
          </span>
          <v-text-field
            :value="forMainDialog.headline"
            outlined
            dense
            hide-details
            class="mt-1"
            @input="setProperty('headline', $event)"
          />
        </label>
        <v-tooltip
          bottom
          max-width="400"
        >
          <template #activator="{ on }">
            <span
              class="color-primary"
              v-on="on"
            >
              {{ $t('common.availableSpintax') }}
            </span>
          </template>
          <span>{{ $t('common.availableSpintaxHint') }}</span>
        </v-tooltip>
      </div>
      <!-- ЗАГОЛОВОК КОНЕЦ -->

      <!-- ТЕКСТ -->
      <div class="mb-3">
        <label>
          <span class="font-16">
            {{ $t('common.adText') }}
          </span>
          <v-textarea
            :value="forMainDialog.text"
            outlined
            rows="3"
            hide-details
            class="mt-1"
            @input="setProperty('text', $event)"
          />
        </label>
        <v-tooltip
          bottom
          max-width="400"
        >
          <template #activator="{ on }">
            <span
              class="color-primary"
              v-on="on"
            >
              {{ $t('common.availableSpintax') }}
            </span>
          </template>
          <span>{{ $t('common.availableSpintaxHint') }}</span>
        </v-tooltip>
      </div>
      <!-- ТЕКСТ КОНЕЦ -->

      <!-- ОПИСАНИЕ -->
      <div class="mb-3">
        <label>
          <span class="font-16">
            {{ $t('common.description') }}
          </span>
          <v-textarea
            :value="forMainDialog.description"
            outlined
            rows="3"
            hide-details
            class="mt-1"
            @input="setProperty('description', $event)"
          />
        </label>
        <v-tooltip
          bottom
          max-width="400"
        >
          <template #activator="{ on }">
            <span
              class="color-primary"
              v-on="on"
            >
              {{ $t('common.availableSpintax') }}
            </span>
          </template>
          <span>{{ $t('common.availableSpintaxHint') }}</span>
        </v-tooltip>
      </div>
      <!-- ОПИСАНИЕ КОНЕЦ -->

      <!-- ПРИЗЫВ К ДЕЙСТВИЮ -->
      <label>
        <span class="font-16">
          {{ $t('upload.wizard.callToAction') }}
          <v-tooltip
            bottom
            max-width="300"
          >
            <template #activator="{ on }">
              <v-icon
                :size="14"
                class="ml-1 cursor-pointer"
                v-on="on"
              >
                far fa-question-circle
              </v-icon>
            </template>
            <span>{{ $t('upload.wizard.callToActionHint') }}</span>
          </v-tooltip>
        </span>
        <v-autocomplete
          :value="forMainDialog.call_to_action"
          :items="callToActionMapped"
          single-line
          outlined
          dense
          hide-details
          class="mt-1"
          @change="setProperty('call_to_action', $event)"
        />
      </label>
      <!-- ПРИЗЫВ К ДЕЙСТВИЮ КОНЕЦ -->
    </v-card-text>
  </v-card>
</template>

<script>
import callToAction          from '@/constants/bundles/call_to_action';
import { mapGetters }        from 'vuex';

import DialogAvailableMacros from './UploadBundlesDialogsMainDialogAdsMainAvailableMacros.vue';

export default {
  name: 'UploadBundlesDialogsMainDialogAdsetsMain',

  components: {
    DialogAvailableMacros
  },

  data() {
    return {
      dialogs: {
        availableMacros: false,
      }
    };
  },

  computed: {
    ...mapGetters({
      forMainDialog: 'bundles/forMainDialog',
    }),

    callToActionMapped() {
      return callToAction.map(i => {
        return {
          text: this.$t(`upload.bundles.callToAction.${i.toLowerCase()}`),
          value: i
        };
      });
    }
  },

  methods: {
    setProperty(key, value) {
      this.$store.dispatch('bundles/setForMainDialogProperty', { key, value });
    }
  }
};
</script>

