<template>
  <v-card class="create-bundle-box-shadow mt-4">
    <!-- НАЗВАНИЕ КАРТОЧКИ -->
    <v-card-title class="pb-3">
      {{ $t('upload.bundles.mainDialog.cardsTitles.demography') }}
    </v-card-title>
    <!-- НАЗВАНИЕ КАРТОЧКИ КОНЕЦ -->

    <v-card-text>
      <v-row class="font-16">
        <!-- ВОЗРАСТ -->
        <v-col cols="2">
          {{ $t('common.age') }}
        </v-col>
        <v-col
          cols="10"
          class="d-flex align-center"
        >
          <v-autocomplete
            :value="forMainDialog.age.min"
            :items="ages"
            outlined
            dense
            hide-details
            @change="setAge('min', $event)"
          >
            <template #item="{item}">
              {{ item === 65 ? '65+' : item }}
            </template>
            <template #selection="{item}">
              {{ item === 65 ? '65+' : item }}
            </template>
          </v-autocomplete>
          <v-autocomplete
            :value="forMainDialog.age.max"
            :items="ages"
            outlined
            dense
            hide-details
            class="ml-2"
            @change="setAge('max', $event)"
          >
            <template #item="{item}">
              {{ item === 65 ? '65+' : item }}
            </template>
            <template #selection="{item}">
              {{ item === 65 ? '65+' : item }}
            </template>
          </v-autocomplete>
          <v-switch
            :input-value="forMainDialog.randomize_age"
            :label="$t('common.randomize')"
            class="pt-0 mt-0 ml-3"
            dense
            hide-details
            @change="setProperty('randomize_age', $event)"
          />
          <v-text-field
            v-show="forMainDialog.randomize_age"
            :value="forMainDialog.age_random"
            outlined
            dense
            hide-details
            type="number"
            min="0"
            class="centralize-append-outer centralize-prepend-outer ml-2"
            @blur="handleBlur('age_random')"
            @input="setProperty('age_random', $event)"
          >
            <template #prepend>
              &plusmn;
            </template>
            <template #append-outer>
              {{ $t('common.years').toLocaleLowerCase() }}
            </template>
          </v-text-field>
          <v-tooltip
            v-show="forMainDialog.randomize_budget"
            bottom
            max-width="300"
          >
            <template #activator="{ on }">
              <v-icon
                :size="14"
                class="ml-2 cursor-pointer"
                v-on="on"
              >
                far fa-question-circle
              </v-icon>
            </template>
            <span>{{ $t('common.ageHint') }}</span>
          </v-tooltip>
        </v-col>
        <!-- ВОЗРАСТ КОНЕЦ -->

        <!-- ПОЛ -->
        <v-col cols="2">
          {{ $t('common.gender') }}
        </v-col>
        <v-col cols="10">
          <v-btn-toggle
            :value="genders"
            color="primary"
            mandatory
            multiple
            dense
            borderless
            @change="handleChangeGenders"
          >
            <v-btn
              value="any"
              small
              v-text="$t('common.any')"
            />
            <v-btn
              value="male"
              small
              v-text="$t('common.genderMale')"
            />
            <v-btn
              value="female"
              small
              v-text="$t('common.genderFemale')"
            />
          </v-btn-toggle>
        </v-col>
        <!-- ПОЛ КОНЕЦ -->
      </v-row>
    </v-card-text>
  </v-card>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  name: 'UploadBundlesDialogsMainDialogAdsetsDemography',

  data() {
    return {
      ages: this.getAges(),
      genders: []
    };
  },

  computed: {
    ...mapGetters({
      forMainDialog: 'bundles/forMainDialog',
    }),
  },

  created() {
    if (this.forMainDialog.genders.male) {
      this.genders.push('male');
    }
    if (this.forMainDialog.genders.female) {
      this.genders.push('female');
    }
    if (this.forMainDialog.genders.any) {
      this.genders = ['any'];
    }
  },

  methods: {
    getAges() {
      const ages = [];

      for (let i = 13; i <= 65; i++) {
        ages.push(i);
      }

      return ages;
    },

    setAge(key, value) {
      const age = {
        min: this.forMainDialog.age.min,
        max: this.forMainDialog.age.max,
      };

      age[key] = value;

      this.setProperty('age', age);
    },

    handleChangeGenders(values) {
      if (values[values.length - 1] === 'any') {
        this.genders = ['any'];
      } else {
        this.genders = values.filter(i => i !== 'any');
      }

      this.setGenders();
    },

    setGenders() {
      this.setProperty('genders', {
        male: this.genders.includes('male'),
        female: this.genders.includes('female'),
        any: this.genders.includes('any'),
      });
    },

    setProperty(key, value) {
      this.$store.dispatch('bundles/setForMainDialogProperty', { key, value });
    },

    handleBlur(key) {
      if (this.forMainDialog[key] < 0) {
        this.setProperty(key, 0);
      }
    }
  }
};
</script>

