<template>
  <v-card class="create-bundle-box-shadow mt-4">
    <!-- НАЗВАНИЕ КАРТОЧКИ -->
    <v-card-title class="pb-3">
      {{ $t('tracker.tabs.conversions') }}
    </v-card-title>
    <!-- НАЗВАНИЕ КАРТОЧКИ КОНЕЦ -->

    <v-card-text>
      <!-- СОБЫТИЯ ПИКСЕЛЯ -->
      <label class="font-16">
        {{ $t('common.pixelEvent') }}
        <v-tooltip
          bottom
          max-width="300"
        >
          <template #activator="{ on }">
            <v-icon
              :size="14"
              class="ml-1 cursor-pointer"
              v-on="on"
            >
              far fa-question-circle
            </v-icon>
          </template>
          <span>{{ $t('upload.pixel_events.pixelEventsHint') }}</span>
        </v-tooltip>
        <v-select
          :value="forMainDialog.pixel_event"
          :item-text="item => $t(`upload.pixel_events.${item.text}`)"
          :item-value="item => item.value"
          :items="pixelEvents"
          outlined
          dense
          hide-details
          class="mt-1 mb-3"
          @change="setProperty('pixel_event', $event)"
        />
      </label>
      <!-- СОБЫТИЯ ПИКСЕЛЯ КОНЕЦ -->

      <!-- ОКНО КОНВЕРСИИ -->
      <label class="font-16">
        {{ $t('upload.conversionWindow.conversionWindow') }}
        <v-tooltip
          bottom
          max-width="300"
        >
          <template #activator="{ on }">
            <v-icon
              :size="14"
              class="ml-1 cursor-pointer"
              v-on="on"
            >
              far fa-question-circle
            </v-icon>
          </template>
          <span>{{ $t('upload.conversionWindow.conversionWindowHint') }}</span>
        </v-tooltip>
        <v-select
          :value="forMainDialog.conversion_window"
          :item-text="item => $t(`upload.conversionWindow.${item.text}`)"
          :item-value="item => item.value"
          :items="conversionWindows"
          outlined
          dense
          hide-details
          class="mt-1"
          @change="setProperty('conversion_window', $event)"
        />
      </label>
      <!-- ОКНО КОНВЕРСИИ КОНЕЦ -->
    </v-card-text>
  </v-card>
</template>

<script>
import conversionWindows from '@/constants/bundles/conversion_windows';
import pixelEvents       from '@/constants/bundles/pixel_events';
import { mapGetters }    from 'vuex';

export default {
  name: 'UploadBundlesDialogsMainDialogAdsetsConversions',

  data() {
    return {
      pixelEvents,
      conversionWindows
    };
  },

  computed: {
    ...mapGetters({
      forMainDialog: 'bundles/forMainDialog',
    }),
  },

  methods: {
    setProperty(key, value) {
      this.$store.dispatch('bundles/setForMainDialogProperty', { key, value });
    }
  }
};
</script>

