<template>
  <div>
    <v-card>
      <v-card-text style="padding: 0px 8px 0px 8px">
        <!-- ТАБЛИЦА -->
        <v-data-table
          :value="selected"
          :headers="cols"
          fixed-header
          :items="lists.filtered"
          :items-per-page="50"
          :footer-props="{'items-per-page-options': [10, 30, 50, 100, -1]}"
          :height="innerHeight - 50 - 48 - 36"
          show-select
          @input="selectLists($event)"
        >
          <!-- ID -->
          <template #item.id="{item}">
            {{ item.id }}
          </template>

          <!-- НАЗВАНИЕ + ВЛАДЕЛЕЦ -->
          <template #item.name="{item}">
            <main-table-name :item="item" />
          </template>

          <!-- ТИП -->
          <template #item.type="{item}">
            <main-table-type :item="item" />
          </template>

          <!-- ЭЛЕМЕНТЫ -->
          <template #item.elements="{item}">
            <main-table-elements :item="item" />
          </template>
        </v-data-table>
      </v-card-text>
    </v-card>
  </div>
</template>

<script>
import {mapGetters}                           from 'vuex';

import MainTableElements                      from './UploadListsMainTableElements';
import MainTableName                          from './UploadListsMainTableName';
import MainTableType                          from './UploadListsMainTableType';

export default {
  name: 'UploadListsMainTable',

  components: {
    MainTableElements,
    MainTableName,
    MainTableType
  },

  data() {
    return {
      cols: [
        {
          text: 'ID',
          value: 'id',
          width: 35,
        },
        {
          text: this.$t('common.name'),
          value: 'name',
          width: 300,
        },
        {
          text: this.$t('common.type'),
          value: 'type',
          width: 150,
        },
        {
          text: this.$t('common.elements'),
          value: 'elements',
          // width: 50,
        },
      ]
    };
  },

  computed: {
    ...mapGetters({
      lists: 'upload_lists/lists',
      innerHeight: 'main/innerHeight',
      selected: 'upload_lists/selected',
    })
  },
  
  methods: {
    selectLists(lists) {
      this.$store.dispatch('upload_lists/setSelectedLists', lists);
    }
  }

};
</script>

