<template>
  <div>
    <!-- МОДАЛЬНОЕ ОКНО -->
    <v-dialog
      :value="true"
      max-width="500px"
      scrollable
      persistent
    >
      <v-card>
        <!-- ЗАГОЛОВОК МОДАЛЬНОГО ОКНА -->
        <v-card-title>
          <span>
            {{ dialogMode === 'create' ? $t('common.createNewList') + ' (' + $t(`common.${list.type}`).toLowerCase() + ')' : $t('common.editList') + ' ' + list.name }}
          </span>
        </v-card-title>
        <v-card-text style="max-height: 700px;">
          <!-- НАЗВАНИЕ -->
          <v-text-field
            v-model="list.name"
            class="pt-2" 
            :label="$t('common.name')"
            outlined
            dense
            hide-details
          />
          <!-- МУЛЬТИСЕЛЕКТ -->
          <v-select
            v-model="list.items"
            :options="searchResults"
            :get-option-key="item => list.type === 'geo' ? item.fb_key + item.id : item.id"
            label="name"
            :placeholder="$t('common.elements')"
            :multiple="true"
            class="pt-2 main-dialog-select"
            @search="search"
          >
            <!-- ШАБЛОН: НЕТ ВАРИАНТОВ -->
            <template #no-options="">
              {{ $t('common.noOptions') }}
            </template>

            <!-- ШАБЛОН ДЛЯ ГЕО -->
            <template
              v-if="list.type === 'geo'"
              #option="geo"
            >
              <div style="margin: 0">
                {{ geo.name }}
              </div>
              <div style="font-size: 10px;">
                {{ typeof geo.country_name !== 'undefined' ? geo.country_name : '' }}
                {{ typeof geo.region !== 'undefined' ? ' / ' + geo.region : '' }}
              </div>
            </template>

            <!-- ШАБЛОН ДЛЯ ИНТЕРЕСОВ И ПОВЕДЕНИЯ -->
            <template
              v-else
              #option="{ name, path }"
            >
              <div style="margin: 0">
                {{ name }}
              </div>
              <div style="font-size: 10px;">
                {{ path.slice(0, path.length - 1).join(' / ') }}
              </div>
            </template>
          </v-select>

          <!-- СВИТЧ СДЕЛАТЬ ОБЩИМ ДЛЯ ВСЕХ -->
          <v-switch
            v-model="list.shared_to_all"
            :label="$t('common.shareToAll')"
            dense
            hide-details
          />

          <!-- МУЛЬТИСЕЛЕКТ РАЗДАТЬ ПОЛЬЗОВАТЕЛЯМ -->
          <v-combobox
            v-if="!list.shared_to_all"
            class="mt-4 lists-create-list-combobox"
            :label="$t('common.shareToUsers')"
            :item-text="user => user.display_name || user.login"
            :items="allUsers"
            :value="selectedUsers"
            multiple
            chips
            deletable-chips
            clearable
            small-chips
            single-line
            outlined
            dense
            @change="shareToUsers"
          />
        </v-card-text>
        <v-card-actions>
          <v-spacer />
          <!-- КНОПКА ОТМЕНА -->
          <v-btn
            text
            color="grey"
            @click="$store.dispatch('upload_lists/closeDialog', 'createList'), $store.dispatch('upload_lists/clearList')"
          >
            {{ $t('common.close') }}
          </v-btn>

          <!-- КНОПКА СОЗДАТЬ -->
          <v-btn
            v-if="dialogMode === 'create'"
            color="blue darken-1"
            text
            :loading="loading.createList"
            @click="create"
          >
            {{ $t('common.create') }}
          </v-btn>

          <!-- КНОПКА СОХРАНИТЬ -->
          <v-btn
            v-if="dialogMode === 'edit'"
            color="blue darken-1"
            text
            :loading="loading.saveList"
            @click="save"
          >
            {{ $t('common.save') }}
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import 'vue-select/dist/vue-select.css';
import vSelect        from 'vue-select';
import {mapGetters}   from 'vuex';

export default {
  name: 'UploadListsDialogCreateList',

  components: {
    vSelect
  },

  data() {
    return {
      searchResults: [],
      lastSearch: null,
      allUsers: [],
      selectedUsers: []
    };
  },

  computed: {
    ...mapGetters({
      dialogs: 'upload_lists/dialogs',
      list: 'upload_lists/list',
      loading: 'upload_lists/loading',
      dialogMode: 'upload_lists/dialogMode',
      idForEdit: 'upload_lists/idForEdit'
    })
  },

  async created () {
    await this.loadUsers();
    if (this.dialogMode === 'edit' && this.list.permissions && this.list.permissions.length > 0) {
      this.allUsers.map(user => {
        for (let y = 0; y < this.list.permissions.length; y++) {
          if(user.id === parseInt(this.list.permissions[y])) {
            this.selectedUsers.push(user);
          }
        }
      });
    }
  },

  methods: {
    async loadUsers() {
      const response = await this.api('/users/all');
      if (typeof response.data.data !== 'undefined') {
        this.allUsers = response.data.data;
      }
    },

    search(item) {
      if (!item) return;
      this.lastSearch = item;
      setTimeout(() => {
        if (item === this.lastSearch) {
          if (this.list.type === 'geo') {
            this.api.post(`/fb_geo?type=all&q=${item}`)
              .then(response => {
                this.searchResults = response.data;
              });
          } else {
            this.api.post(`/fb_lists/${this.list.type}?q=${item}`)
              .then(response => {
                this.searchResults = response.data.data;
              });
          }
        }
      }, 1000);
    },

    shareToUsers(users) {
      this.list.permissions = users.map(user => user.id + 'view');
    },

    async create() {
      await this.$store.dispatch('upload_lists/createList', this.list);
      this.$store.dispatch('upload_lists/clearList');
      this.$store.dispatch('upload_lists/closeDialog', 'createList');
    },

    async save() {
      let data = {
        id: this.idForEdit,
        type: this.list.type,
        name: this.list.name,
        items: this.list.items,
        shared_to_all: this.list.shared_to_all,
        permissions: this.list.permissions
      };
      await this.$store.dispatch('upload_lists/saveList', data);
      this.$store.dispatch('upload_lists/clearList');
      this.$store.dispatch('upload_lists/closeDialog', 'createList');
    }
  }
};
</script>
<style>
.vs__dropdown-menu {
  max-height: 140px !important;
}
.lists-create-list-combobox .v-select__selections {
  padding: 6px 0;
}
</style>