<template>
  <v-card class="wizard-box-shadow">
    <!-- НАЗВАНИЕ КАРТОЧКИ -->
    <v-card-title class="pb-3">
      {{ $t('common.bundle') }}
    </v-card-title>
    <!-- НАЗВАНИЕ КАРТОЧКИ КОНЕЦ -->

    <!-- ВЫБЕРИТЕ СВЯЗКУ ИЛИ СОЗДАЙТЕ НОВУЮ -->
    <v-card-text class="d-flex align-center">
      <v-autocomplete
        :label="$t('upload.wizard.chooseOne')"
        :items="['Свитеры m25+ KZ']"
        outlined
        dense
        hide-details
        clearable
        @change="handleChange"
      />
      <v-btn
        color="primary"
        class="ml-2"
        text
        @click="createBundle"
      >
        <v-icon
          left
          small
        >
          fas fa-plus
        </v-icon>
        {{ $t('upload.bundles.topbar.createBundle') }}
      </v-btn>
    </v-card-text>
    <!-- ВЫБЕРИТЕ СВЯЗКУ ИЛИ СОЗДАЙТЕ НОВУЮ КОНЕЦ -->
  </v-card>
</template>

<script>
import emptyJob from '@/constants/wizard/empty_job';

export default {
  name: 'UploadWizardMainStepperSettingsBundle',

  methods: {
    handleChange(value) {
      if (value) {
        this.$store.dispatch('upload_wizard/setSelectedBundle', {
          name: 'Свитеры m25+ KZ',
        });
        this.$store.dispatch('upload_wizard/setJob', this.$_.cloneDeep(emptyJob));
      } else {
        this.$store.dispatch('upload_wizard/setSelectedBundle', null);
        this.$store.dispatch('upload_wizard/setJob', null);
      }
    },

    async createBundle() {
      await this.$store.dispatch('bundles/setMainDialogMode', 'create');
      await this.$store.dispatch('bundles/setForMainDialog');
      await this.$store.dispatch('bundles/openDialog', 'mainDialog');
    }
  }
};
</script>