<template>
  <v-dialog
    :value="dialogs.mainDialog"
    persistent
    fullscreen
    :transition="false"
  >
    <!-- ДИАЛОГИ -->
    <main-dialog-close-alert
      :close-alert="closeAlert"
      @closeAlertDialog="closeAlert=false"
      @closeDialog="closeDialog"
    />
    <!-- ДИАЛОГИ КОНЕЦ -->

    <v-card class="create-bundle-dialog-card">
      <!-- ХЭДЕР -->
      <main-dialog-header @closeDialog="closeDialog" />
      <!-- ХЭДЕР КОНЕЦ -->

      <v-stepper
        v-model="currentStep"
        class="create-bundle-stepper"
      >
        <!-- ХЭДЕР СТЕППЕРА -->
        <v-stepper-header class="create-bundle-stepper-header">
          <v-stepper-step
            v-for="(step, stepIndex) in steps"
            :key="`UploadBundlesDialogsMainDialog-${step}-${stepIndex}`"
            :step="stepIndex + 1"
            editable
          >
            {{ $t(`adsmanager.tabs.${step}`) }}
          </v-stepper-step>
        </v-stepper-header>
        <!-- ХЭДЕР СТЕППЕРА КОНЕЦ -->

        <v-stepper-items class="create-bundle-stepper-items">
          <!-- ДИАЛОГ КАМПАНИЙ -->
          <v-stepper-content :step="1">
            <main-dialog-campaigns />
          </v-stepper-content>
          <!-- ДИАЛОГ КАМПАНИЙ КОНЕЦ -->

          <!-- ДИАЛОГ АДСЕТОВ -->
          <v-stepper-content :step="2">
            <main-dialog-adsets />
          </v-stepper-content>
          <!-- ДИАЛОГ АДСЕТОВ КОНЕЦ -->

          <!-- ДИАЛОГ ОБЪЯВЛЕНИЙ -->
          <v-stepper-content :step="3">
            <main-dialog-ads />
          </v-stepper-content>
          <!-- ДИАЛОГ ОБЪЯВЛЕНИЙ КОНЕЦ -->
        </v-stepper-items>
      </v-stepper>

      <!-- ГЛАВНАЯ КНОПКА -->
      <v-row class="px-6">
        <v-col
          cols="12"
          sm="10"
          offset-sm="1"
          md="8"
          offset-md="2"
          lg="6"
          offset-lg="3"
          class="d-flex justify-space-between"
        >
          <v-btn
            color="primary"
            class="mb-3"
            outlined
            @click="closeDialog(false)"
          >
            {{ $t('common.cancel') }}
          </v-btn>

          <v-btn
            color="primary"
            class="mb-3"
            :disabled="currentStep === 3"
            @click="currentStep++"
          >
            {{ $t('common.continue') }}
          </v-btn>
        </v-col>
      </v-row>
      <!-- ГЛАВНАЯ КНОПКА КОНЕЦ -->
    </v-card>
  </v-dialog>
</template>

<script>
import { mapGetters }       from 'vuex';

import MainDialogCloseAlert from './UploadBundlesDialogsMainDialogCloseAlert.vue';
import MainDialogHeader     from './UploadBundlesDialogsMainDialogHeader.vue';
import MainDialogAds        from './ads/UploadBundlesDialogsMainDialogAds.vue';
import MainDialogAdsets     from './adsets/UploadBundlesDialogsMainDialogAdsets.vue';
import MainDialogCampaigns  from './campaigns/UploadBundlesDialogsMainDialogCampaigns.vue';

export default {
  name: 'UploadBundlesDialogsMainDialog',

  components: {
    MainDialogAds,
    MainDialogAdsets,
    MainDialogCampaigns,
    MainDialogHeader,
    MainDialogCloseAlert,
  },

  data() {
    return {
      currentStep: 1,
      steps: [ 'campaigns', 'adsets', 'ads' ],
      interacted: false,
      unwatchForMainDialog: null,
      unwatchCurrentStep: null,
      closeAlert: false,
    };
  },

  computed: {
    ...mapGetters({
      dialogs: 'bundles/dialogs',
      forMainDialog: 'bundles/forMainDialog',
    }),
  },

  created() {
    this.unwatchForMainDialog = this.$watch('forMainDialog', this.setInteracted, { deep: true });
    this.unwatchCurrentStep = this.$watch('currentStep', this.setInteracted);
  },

  methods: {
    closeDialog(fromCloseAlert = false) {
      if (this.interacted && !fromCloseAlert) {
        this.closeAlert = true;
        return;
      }

      this.$store.dispatch('bundles/closeDialog', 'mainDialog');
    },

    setInteracted() {
      this.interacted = true;
      this.unwatchForMainDialog();
      this.unwatchCurrentStep();
    }
  }
};
</script>

