<template>
  <!-- ПЕРИОД -->
  <div
    style="float: right;"
  >
    <v-select
      :value="period"
      style="width: 240px;"
      :label="$t('common.period')"
      :item-text="item => $t(`upload.jobs.period.${item.text}`)"
      :item-value="item => item.value"
      :items="periodList"
      class="compact-form"
      single-line
      outlined
      dense
      hide-details
      @change="selectPeriod"
    />
  </div>
</template>

<script>
import periodList           from '@/constants/jobs/period';
import {mapGetters}         from 'vuex';

export default {
  name: 'UploadJobsTopbarPeriod',

  data() {
    return {
      periodList
    };
  },

  computed: {
    ...mapGetters({
      period: 'upload_jobs/period'
    })
  },

  methods: {
    async selectPeriod(period) {
      await this.$store.dispatch('upload_jobs/setPeriod', period);
      this.$store.dispatch('upload_jobs/setJobs', this.period);
    }
  }
};
</script>