<template>
  <v-card class="create-bundle-box-shadow mt-4">
    <!-- НАЗВАНИЕ КАРТОЧКИ -->
    <v-card-title class="pb-3">
      {{ $t('common.interestsAndBehavior') }}
    </v-card-title>
    <!-- НАЗВАНИЕ КАРТОЧКИ КОНЕЦ -->

    <v-card-text>
      <!-- ЯЗЫКИ -->
      <label class="font-16">
        {{ $t('common.languages') }}
        <v-select
          :value="forMainDialog.locales"
          item-text="label"
          item-value="key"
          :items="languages"
          outlined
          dense
          hide-details
          clearable
          multiple
          chips
          small-chips
          deletable-chips
          class="mt-1 mb-3"
          @change="setProperty('locales', $event)"
        />
      </label>
      <!-- ЯЗЫКИ КОНЕЦ -->

      <!-- ТОЛЬКО ПРИ ПОДКЛЮЧЕНИИ К WI-FI -->
      <div class="d-flex align-center">
        <v-switch
          :input-value="forMainDialog.detailed_targeting"
          :label="$t('common.detailedTargeting')"
          class="pt-0 mt-0"
          dense
          hide-details
          @change="setProperty('detailed_targeting', $event)"
        />
        <v-tooltip
          bottom
          max-width="300"
        >
          <template #activator="{ on }">
            <v-icon
              :size="14"
              class="ml-2 cursor-pointer"
              v-on="on"
            >
              far fa-question-circle
            </v-icon>
          </template>
          <span>{{ $t('common.detailedTargetingHint') }}</span>
        </v-tooltip>
      </div>
      <!-- ТОЛЬКО ПРИ ПОДКЛЮЧЕНИИ К WI-FI КОНЕЦ -->
    </v-card-text>
  </v-card>
</template>

<script>
import languages      from '@/constants/upload/fb_languages';
import { mapGetters } from 'vuex';

export default {
  name: 'UploadBundlesDialogsMainDialogAdsetsInterestsAndBehavior',

  data() {
    return {
      languages
    };
  },

  computed: {
    ...mapGetters({
      forMainDialog: 'bundles/forMainDialog',
    }),
  },

  methods: {
    setProperty(key, value) {
      this.$store.dispatch('bundles/setForMainDialogProperty', { key, value });
    },
  }
};
</script>

